import * as React from 'react'
import { withTheme } from 'emotion-theming'
import { cx } from 'emotion'

import { DefaultTheme } from '@style'

import { defaultStyle } from './style'

import { TypographyPropsInterface } from '../types'

class H2 extends React.Component<TypographyPropsInterface> {
  static defaultProps = {
    className: '',
    theme: DefaultTheme
  };

  render() {
    const { children, className, theme, ...rest } = this.props
    return (
      <h2 className={cx(defaultStyle(theme), className)} {...rest}>
        {children}
      </h2>
    )
  }
}

export default withTheme(H2)
