import * as React from 'react'
import { cx } from 'emotion'
import Icon from '@primitives/Icon'

import DesktopNavigationSubsection from './SubSection'
import DesktopNavigationSubsectionColumns from './ColumnSubsection'
import DesktopAuthButtons from './AuthButtons'
import {
  bigScreenNavContainerStyle,
  navLinkContainerStyle,
  navLinkStyle,
  navLinkAnchorStyle,
  iconContainerStyle,
  navLinkAnchorDarkBackgroundStyle,
  navLinkAnchorLightBackgroundThemeStyle,
  openIconContainerStyle,
  logoAndLinksContainerStyle,
  logoContainerStyle,
  sectionLinkContainerStyle,
  chevronIconLightBackgroundStyle,
  chevronIconDarkBackgroundStyle,
  chevronIconStyle,
  opacityTransitionsStyle,
  desktopNavigationWrapperStyle
} from './style'
import { DesktopNavigationInterface } from './types'

class DesktopNavigation extends React.Component<DesktopNavigationInterface> {
  onNavigationItemChanged = (e, isOpen, idx) => {
    const { onNavItemClicked } = this.props
    if (isOpen) {
      return
    }
    onNavItemClicked(idx)
  };

  handleNavSectionClick = (e, isOpen, idx) => {
    console.log('click 2')
    e.nativeEvent.stopImmediatePropagation()
    const { onNavItemClicked } = this.props
    if (isOpen) {
      return onNavItemClicked(-1)
    }
    onNavItemClicked(idx)
  };

  renderNavLinks = () => {
    const { navigation, activeNavItem, lightBackground } = this.props
    return navigation.map((item, idx) => {
      const hasSubSections = item.columns.length > 0
      const isOpen = idx === activeNavItem
      const isColumns = item.columns.length > 1
      if (hasSubSections) {
        return (
          <div
            key={idx}
            className={sectionLinkContainerStyle}
            onTouchStart={e => this.handleNavSectionClick(e, isOpen, idx)}
            onClick={e => this.handleNavSectionClick(e, isOpen, idx)}
            onMouseEnter={e => this.onNavigationItemChanged(e, isOpen, idx)}
            onMouseLeave={e => this.props.onNavItemClicked(-1)}
          >
            <li className={navLinkStyle}>
              <a
                className={cx(navLinkAnchorStyle, {
                  [navLinkAnchorDarkBackgroundStyle]: !lightBackground,
                  [navLinkAnchorLightBackgroundThemeStyle]: lightBackground
                })}
              >
                <span className={desktopNavigationWrapperStyle}>
                  <span className={opacityTransitionsStyle}>{item.title}</span>
                  {isColumns ? (
                    <DesktopNavigationSubsectionColumns
                      columns={item.columns}
                      isOpen={isOpen}
                    />
                  ) : (
                    <DesktopNavigationSubsection
                      sections={item.columns[0]}
                      isOpen={isOpen}
                    />
                  )}
                </span>
                <span className={opacityTransitionsStyle}>
                  <span
                    className={cx(iconContainerStyle, {
                      [openIconContainerStyle]: isOpen
                    })}
                  >
                    <Icon
                      icon="chevron-down"
                      style={18}
                      useDefaultFills={false}
                      className={cx(chevronIconStyle, {
                        [chevronIconDarkBackgroundStyle]: !lightBackground,
                        [chevronIconLightBackgroundStyle]: lightBackground
                      })}
                    />
                  </span>
                </span>
              </a>
            </li>
          </div>
        )
      }
      return (
        <div key={idx} className={sectionLinkContainerStyle}>
          <li className={navLinkStyle}>
            <a
              href={item.href}
              className={cx(navLinkAnchorStyle, {
                [navLinkAnchorDarkBackgroundStyle]: !lightBackground,
                [navLinkAnchorLightBackgroundThemeStyle]: lightBackground
              })}
            >
              <span className={opacityTransitionsStyle}>{item.title}</span>
            </a>
          </li>
        </div>
      )
    })
  };

  render() {
    const { lightBackground, shouldShowDemoButton } = this.props
    return (
      <div className={bigScreenNavContainerStyle}>
        <div className={logoAndLinksContainerStyle}>
          <a className={logoContainerStyle} href={`/`}>
            <Icon
              icon="icon-segment_logo"
              style={140}
              useDefaultFills={false}
              className={cx(chevronIconStyle, {
                [chevronIconDarkBackgroundStyle]: !lightBackground,
                [chevronIconLightBackgroundStyle]: lightBackground
              })}
            />
          </a>
          <ul className={navLinkContainerStyle}>{this.renderNavLinks()}</ul>
        </div>
        <DesktopAuthButtons
          lightBackground={lightBackground}
          shouldShowDemoButton={shouldShowDemoButton}
        />
      </div>
    )
  }
}

export default DesktopNavigation
