import React, { Component, Fragment } from 'react'
import { cx } from 'emotion'
import { withTheme, ThemeProvider } from 'emotion-theming'
import P from '@primitives/Typography/P'
import Link from '@primitives/Link'
import Icon from '@primitives/Icon'

import {
  containerStyle,
  headerStyle,
  homeLinkStyle,
  leftStyle,
  linkStyle,
  notFirstLinksStyle,
  rightStyle,
  hamburgerContainerStyle,
  desktopButtonsContainersStyle,
  tabletAndMobileButtonsContainer,
  overlayStyle,
  openOverlayStyle,
  overlayLinksContainerStyle,
  overlayLinkStyle,
  buttonCtaStyle,
  emailCaptureStyle,
  emailCaptureMobileContainerStyle,
  showSubscriptionOvelayStyle,
  mobileOverlayHasBannerStyle
} from './style'
import SubscriptionOverlay from './subscriptionOverlay'
import { StartupHeaderPropTypesInterface } from './types'

import HamburgerButton from '../../../Primitives/HamburgerButton'
import Button from '../../../Primitives/Button'
import EmailCapture from '../../../Primitives/EmailCapture'
import { PublixTheme } from '../../../Style/types'

class StartupHeader extends Component<StartupHeaderPropTypesInterface> {
  state = {
    isClientSide: false,
    isAcademy: false,
    isStartups: false,
    isSubscriptionOverlayOpen: false,
    isOpen: false
  };

  hasBanner = () => this.props.flags.hasBanner;

  getUpdatedTheme = (ancestorTheme: PublixTheme): PublixTheme => {
    return {
      ...ancestorTheme,
      themeStyle: 'light'
    }
  };

  componentDidMount() {
    this.setState({
      isClientSide: true,
      isAcademy: window && window.location.pathname.startsWith('/academy'),
      isStartups: window && window.location.pathname.startsWith('/startups')
    })
  }

  onSubscribeClicked = () => {
    const { isSubscriptionOverlayOpen } = this.state
    this.setState({
      isSubscriptionOverlayOpen: !isSubscriptionOverlayOpen
    })
  };

  renderApplyLink = () => {
    const { isClientSide, isStartups } = this.state
    if (!isClientSide) {
      return null
    }
    if (isStartups) {
      return (
        <Link
          className={cx(linkStyle, notFirstLinksStyle)}
          action="//app.segment.com/startup-program"
          style="none"
        >
          <span>Apply Now</span>
        </Link>
      )
    }
    return null
  };

  renderSmallDynamicCta = () => {
    const { isClientSide, isAcademy, isStartups } = this.state
    if (!isClientSide) {
      return null
    }
    if (isAcademy) {
      return (
        <div className={emailCaptureMobileContainerStyle}>
          <P>Subscribe to get a new lesson each week!</P>
          <EmailCapture
            style={'primary'}
            submitstyle={'Academy Subscription'}
            submitmessage={'Thanks! Check your inbox for a confirmation email'}
            className={emailCaptureStyle}
            label={'Send to Email'}
          />
        </div>
      )
    } else if (isStartups) {
      return (
        <Link
          className={cx(linkStyle, overlayLinkStyle)}
          action="//app.segment.com/startup-program"
          style="none"
        >
          <span>Apply Now</span>
        </Link>
      )
    }
    return null
  };

  renderSubscribeButton = () => {
    const { isClientSide, isAcademy } = this.state
    if (!isClientSide) {
      return null
    }

    if (isAcademy) {
      return (
        <div>
          <Button
            style="primary"
            className={cx(buttonCtaStyle, notFirstLinksStyle)}
            onClick={this.onSubscribeClicked}
          >
            Subscribe
          </Button>
        </div>
      )
    }
  };

  onClick = () => {
    const { isOpen } = this.state
    this.setState({
      isOpen: !isOpen
    })
  };

  renderDesktopNavigation = () => {
    return (
      <div className={desktopButtonsContainersStyle}>
        <div className={leftStyle}>
          <Link
            className={cx(linkStyle, homeLinkStyle)}
            action="/"
            style="none"
          >
            <Icon icon="icon-segment_logo" style={100} />
          </Link>
          <Link
            className={cx(linkStyle, notFirstLinksStyle)}
            action="/product/connections/"
            style="none"
          >
            <span>Product</span>
          </Link>
          <Link
            className={cx(linkStyle, notFirstLinksStyle)}
            action="/customers"
            style="none"
          >
            <span>Customers</span>
          </Link>
          <Link
            className={cx(linkStyle, notFirstLinksStyle)}
            action="/docs"
            style="none"
          >
            <span>Docs</span>
          </Link>
          <Link
            className={cx(linkStyle, notFirstLinksStyle)}
            action="/academy"
            style="none"
          >
            <span>Academy</span>
          </Link>
          <Link
            className={cx(linkStyle, notFirstLinksStyle)}
            action="/blog"
            style="none"
          >
            <span>Blog</span>
          </Link>
          {this.renderApplyLink()}
        </div>
        <div className={rightStyle}>{this.renderSubscribeButton()}</div>
      </div>
    )
  };

  renderMobileNavigation = () => {
    const { isOpen } = this.state
    return (
      <div className={tabletAndMobileButtonsContainer}>
        <div className={leftStyle}>
          <Link
            className={cx(linkStyle, homeLinkStyle)}
            action="/"
            style="none"
          >
            <Icon icon="icon-segment_logo" style={100} />
          </Link>
        </div>
        <div className={hamburgerContainerStyle}>
          <HamburgerButton
            isOpen={isOpen}
            style="blue"
            onHamburgerClicked={this.onClick}
          />
        </div>
      </div>
    )
  };

  renderMobileOverlay = () => {
    const { isOpen } = this.state
    const bannerShown = this.hasBanner() && this.props.flags.bannerHidden
    return (
      <div
        className={cx(overlayStyle, {
          [openOverlayStyle]: isOpen
        })}
      >
        <div
          className={cx(overlayLinksContainerStyle, {
            [mobileOverlayHasBannerStyle]: bannerShown
          })}
        >
          <Link
            className={cx(linkStyle, overlayLinkStyle)}
            action="/product"
            style="none"
          >
            <span>Product</span>
          </Link>
          <Link
            className={cx(linkStyle, overlayLinkStyle)}
            action="/customers"
            style="none"
          >
            <span>Customers</span>
          </Link>
          <Link
            className={cx(linkStyle, overlayLinkStyle)}
            action="/docs"
            style="none"
          >
            <span>Docs</span>
          </Link>
          <Link
            className={cx(linkStyle, overlayLinkStyle)}
            action="/academy"
            style="none"
          >
            <span>Academy</span>
          </Link>
          <Link
            className={cx(linkStyle, overlayLinkStyle)}
            action="/blog"
            style="none"
          >
            <span>Blog</span>
          </Link>
          {this.renderSmallDynamicCta()}
        </div>
      </div>
    )
  };

  getAcademyStyles = () => {
    const { isAcademy } = this.state
    if (isAcademy) {
      const { isSubscriptionOverlayOpen } = this.state
      return cx({
        [showSubscriptionOvelayStyle]: isSubscriptionOverlayOpen
      })
    }
    return cx()
  };

  render() {
    const { isAcademy } = this.state
    return (
      <ThemeProvider theme={this.getUpdatedTheme}>
        <Fragment>
          <nav className={cx(headerStyle, this.getAcademyStyles())}>
            {false && isAcademy && <SubscriptionOverlay />}
            <div className={cx(containerStyle)}>
              {this.renderDesktopNavigation()}
              {this.renderMobileNavigation()}
            </div>
          </nav>
          {this.renderMobileOverlay()}
        </Fragment>
      </ThemeProvider>
    )
  }
}

export default withTheme(StartupHeader)
