import React, { Component } from 'react'
import { cx } from 'emotion'
import P from '@primitives/Typography/P'
import Button from '@primitives/Button'
import Link from '@primitives/Link'

import {
  bannerStyle,
  buttonStyle,
  iconStyle,
  textStyle,
  hiddenStyle
} from './style'

import { sharedStyle } from '../sharedStyle'

class StartupBanner extends Component<{ hidden: boolean }> {
  static defaultProps = {
    hidden: false
  };

  render() {
    const { hidden } = this.props
    return (
      <Link
        className={cx(bannerStyle, sharedStyle, { [hiddenStyle]: hidden })}
        style="none"
        action="/startups"
      >
        <P className={textStyle}>
          Early-stage startups get Segment for free.
          <img
            src="//images.ctfassets.net/9u2t3ap6pctb/17BBIyooF4yDFENpZqkFDM/49f25ad1146ec4afc226ae792b46d746/icon-startupbanner.svg"
            className={iconStyle}
          />
          <Button style="secondary" className={buttonStyle}>
            Learn More
          </Button>
        </P>
      </Link>
    )
  }
}

export default StartupBanner
