import React, { Component, StrictMode } from 'react'
import ErrorStackParser from 'error-stack-parser'
import Raven from 'raven'

import { trackingPlan } from './tracking-plan'

import { SENTRY_DSN, isProd, APP_ENV, TAG } from '../../../../config'

export const startRaven = () => {
  try {
    if (isProd) {
      Raven.config(SENTRY_DSN, {
        release: TAG,
        environment: APP_ENV,
        autoBreadcrumbs: true,
        tags: {
          isClientSide: true,
          isServerSide: false
        }
      }).install()
    }
  } catch (e) {
    console.error(e)
  }
}

export class ErrorBoundary extends Component<any> {
  componentDidCatch(error, info) {
    trackError('errorBoundary', error, info)
  }

  render() {
    return <StrictMode>{this.props.children}</StrictMode>
  }
}

const removeNodeModules = stack => {
  return stack
    .filter(line => !line.fileName.includes('node_modules'))
    .map(l => l.toString())
    .join('\n')
}

const extractFilename = stack => {
  const filename = stack.length && stack[0].fileName

  if (!filename) {
    return
  }

  const splitted = filename.split('/')
  const fileNameWithoutPrefix = splitted[splitted.length - 1]
  return fileNameWithoutPrefix
}

const extractFunctionName = stack => {
  const functionName = stack.length && stack[0].functionName
  return functionName
}

export const jsErrorDataExtraction = (error: Error) => {
  const stack = ErrorStackParser.parse(error)
  const stackTrace = removeNodeModules(stack)
  const fileName = extractFilename(stack)
  const functionName = extractFunctionName(stack)
  return {
    stackTrace,
    fileName,
    functionName
  }
}

export const trackError = (
  errorName: string,
  error: Error,
  extra: Object = {}
) => {
  console.error(error)
  Raven.captureException(error, {
    extra
  })
  const errorId = Raven.lastEventId()
  const { fileName, functionName, stackTrace } = jsErrorDataExtraction(error)
  trackingPlan.jsError({
    file_name: fileName,
    function_name: functionName,
    error_name: errorName,
    stack_trace: stackTrace,
    extra: {
      ...extra,
      errorId
    }
  })
}
