import * as React from 'react'
import { cx } from 'emotion'
import Icon from '@primitives/Icon'

import {
  mobileNavLinkContainerStyle,
  mobileNavLinkStyle,
  navLinkContainerStyle
} from './style'
import { NavLinksInterface } from './types'

import {
  chevronIconStyle,
  iconContainerStyle,
  navLinkAnchorStyle,
  navLinkAnchorLightBackgroundThemeStyle,
  chevronIconLightBackgroundStyle,
  openIconContainerStyle
} from '../../DesktopNavigation/style'
import NavSubsection from '../NavSubsection'

class NavLinks extends React.Component<NavLinksInterface> {
  onScroll = (e: React.UIEvent<HTMLUListElement>) => {
    const { setScrolledState } = this.props
    const { scrollTop } = e.currentTarget
    setScrolledState(scrollTop !== 0)
  };

  render() {
    const { activeNavItem, navigation, onNavItemClicked } = this.props
    return (
      <ul className={navLinkContainerStyle} onScroll={this.onScroll}>
        <div>
          {navigation.map((item, index) => {
            const hasSubSections = item.columns.length > 0
            const isOpen = index === activeNavItem
            const reducedColumns = item.columns.reduce(
              (prev: any[], current) => {
                return [...prev, ...current.links]
              },
              []
            )
            if (hasSubSections) {
              return (
                <div className={mobileNavLinkContainerStyle} key={index}>
                  <li
                    className={mobileNavLinkStyle}
                    onClick={e => {
                      e.preventDefault()
                      onNavItemClicked(index)
                    }}
                  >
                    <a
                      className={cx(
                        navLinkAnchorStyle,
                        navLinkAnchorLightBackgroundThemeStyle
                      )}
                    >
                      {item.title}
                      <span
                        className={cx(iconContainerStyle, {
                          [openIconContainerStyle]: isOpen
                        })}
                      >
                        <Icon
                          icon="chevron-down"
                          style={18}
                          className={cx(
                            chevronIconStyle,
                            chevronIconLightBackgroundStyle
                          )}
                        />
                      </span>
                    </a>
                  </li>
                  <NavSubsection sections={reducedColumns} isOpen={isOpen} />
                </div>
              )
            }
            return (
              <div className={mobileNavLinkContainerStyle} key={index}>
                <li className={mobileNavLinkStyle}>
                  <a
                    href={item.href}
                    className={cx(
                      navLinkAnchorStyle,
                      navLinkAnchorLightBackgroundThemeStyle
                    )}
                  >
                    {item.title}
                  </a>
                </li>
              </div>
            )
          })}
        </div>
      </ul>
    )
  }
}

export default NavLinks
