import * as React from 'react'
import HamburgerButton from '@primitives/HamburgerButton'
import Icon from '@primitives/Icon'
import { $segmentDark, $segmentWhite } from '@style/colors'
import { cx } from 'emotion'

import {
  topbarContainerStyle,
  containerStyle,
  logoStyle,
  navigationOpenContainerStyle,
  hamburgerButtonStyle,
  mobileGutterStyle,
  scrolledShadowStyle
} from './style'
import NavLinks from './NavLinks'
import InlineAuthButtons from './InlineAuthButtons'

import { NavigationSectionInterface } from '../types'

class MidSizeNavigation extends React.Component<{
  navigation: NavigationSectionInterface[];
  activeNavItem: number;
  hasBanner: boolean;
  lightBackground: boolean;
  onNavItemClicked: (itemIndex: number) => void;
}> {
  static LockedScrollClass = 'locked-scroll';

  state = {
    isScrolled: false,
    isOpen: false
  };

  MediaQueryMatch: MediaQueryList | null = null;

  componentDidMount() {
    window.addEventListener('resize', this.calculateHeight, { passive: true })
    this.MediaQueryMatch = window.matchMedia('(orientation: portrait)')
    this.MediaQueryMatch.addListener(this.onMediaQuery)
    this.calculateHeight()
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.calculateHeight)
    if (this.MediaQueryMatch) {
      this.MediaQueryMatch.removeListener(this.onMediaQuery)
    }
  }

  onMediaQuery = m => {
    this.calculateHeight()
  };

  calculateHeight = () => {
    const vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)
  };

  componentDidUpdate() {
    const { isOpen } = this.state
    if (isOpen) {
      document.body.classList.add(MidSizeNavigation.LockedScrollClass)
    } else {
      document.body.classList.remove(MidSizeNavigation.LockedScrollClass)
    }
  }

  renderTopBar = () => {
    const { lightBackground } = this.props
    const { isOpen, isScrolled } = this.state
    return (
      <div
        className={cx(topbarContainerStyle, mobileGutterStyle, {
          [scrolledShadowStyle]: isScrolled
        })}
      >
        <a className={logoStyle} href="/">
          <Icon
            icon="icon-segment_logo"
            style={140}
            color={lightBackground || isOpen ? $segmentDark : $segmentWhite}
          />
        </a>
        <HamburgerButton
          isOpen={isOpen}
          onHamburgerClicked={() => {
            this.setState({
              isOpen: !isOpen
            })
          }}
          style={lightBackground || isOpen ? 'dark' : 'white'}
          className={hamburgerButtonStyle}
        />
      </div>
    )
  };

  setScrolledState = (isScrolled: boolean) => {
    this.setState({
      isScrolled
    })
  };

  render() {
    const { isOpen } = this.state
    const {
      hasBanner,
      activeNavItem,
      navigation,
      onNavItemClicked
    } = this.props
    return (
      <div
        className={cx(containerStyle, {
          [navigationOpenContainerStyle(hasBanner)]: isOpen
        })}
      >
        {this.renderTopBar()}
        {isOpen && (
          <NavLinks
            setScrolledState={this.setScrolledState}
            activeNavItem={activeNavItem}
            navigation={navigation}
            onNavItemClicked={onNavItemClicked}
          />
        )}
        {isOpen && <InlineAuthButtons />}
      </div>
    )
  }
}

export default MidSizeNavigation
