import { css } from 'emotion'
import { PublixTheme } from '@style/types'
import {
  $mobileMax,
  $tabletMin,
  $tabletMax,
  $smDesktopMin,
  $smDesktopMax,
  $mdDesktopMin,
  $mdDesktopMax,
  $lgDesktopMin
} from '@style/variables'

export const defaultStyle = (theme: PublixTheme) => {
  const { bodyModules } = theme.fonts
  const { fonts: defaultFonts } = theme
  const { fonts } = theme[theme.themeStyle]
  return css`
    color: ${fonts.h5.color};
    font-weight: ${defaultFonts.bold}; /*TODO: Add override for font-weight*/
    font-family: ${fonts.h5.fontFamily
    ? fonts.h5.fontFamily
    : defaultFonts.headlineFont};
    margin: 0;
    letter-spacing: ${bodyModules.h5.letterSpacing};
    line-height: ${bodyModules.h5.lineHeight};

    @media (min-width: ${$lgDesktopMin}) {
      font-size: ${bodyModules.h5.yugeDesktop};
    }
    @media (max-width: ${$mdDesktopMax}) and (min-width: ${$mdDesktopMin}) {
      font-size: ${bodyModules.h5.largeDesktop};
    }
    @media (max-width: ${$smDesktopMax}) and (min-width: ${$smDesktopMin}) {
      font-size: ${bodyModules.h5.smallDesktop};
    }
    @media (max-width: ${$tabletMax}) and (min-width: ${$tabletMin}) {
      font-size: ${bodyModules.h5.tablet};
    }
    @media (max-width: ${$mobileMax}) {
      font-size: ${bodyModules.h5.mobile};
    }
    &:not(:first-child) {
      margin-top: ${defaultFonts.bodyModules.h5.marginTop};

      img + &,
      label + & {
        margin-top: 1.5rem;
      }
    }
  `
}
