import React, { Component } from 'react'
import { cx } from 'emotion'
import Link from '@primitives/Link'

import { bannerStyle, textStyle, hiddenStyle } from './style'

import { sharedStyle } from '../sharedStyle'

class ProtocolsBanner extends Component<{ hidden: boolean }> {
  static defaultProps = {
    hidden: false
  };

  render() {
    const { hidden } = this.props
    return (
      <div className={cx(bannerStyle, sharedStyle, { [hiddenStyle]: hidden })}>
        <Link style="none" className={textStyle} action="/protocols" />
      </div>
    )
  }
}

export default ProtocolsBanner
