import React, { Component, Fragment } from 'react'
import personalEmailDomains from 'personal-email-domains'
import { cx } from 'emotion'
import H5 from '@primitives/Typography/H5'
import { withTheme } from 'emotion-theming'

import { formStyle, inputStyle, buttonStyle, lightInputStyle } from './style'
import {
  EmailCapturePropsInterface,
  EmailCaptureStateInterface
} from './types'
import NewEmailCapture from './NewEmailCapture'

import { trackingPlan } from '../../lib/tracking-plan'
import { encodeEmail } from '../../lib/Util'
import Button from '../Button'
import { withUserContext } from '../../Context/UserContext'

class EmailCapture extends Component<
  EmailCapturePropsInterface,
  EmailCaptureStateInterface
> {
  static defaultProps = {
    className: '',
    demotype: '',
    email: '',
    formid: '',
    label: 'Try Segment free',
    moduleId: 'NO_MODULE_ID',
    placeholder: 'Your work email',
    redirect: '',
    style: 'primary',
    submitmessage: "Thanks for your interest! You'll be hearing from us soon.",
    submitstyle: ''
  };

  constructor(props) {
    super(props)
    this.state = {
      email: props.email,
      submitted: false
    }
  }

  checkForBusinessEmail = (email, encryptedEmail) => {
    if (this.isPersonalEmail(email)) {
      location.href = `/signup?email=${encryptedEmail}`
    } else {
      this.forceDemoRequest(encryptedEmail)
    }
  };

  forceSignup = encryptedEmail => {
    location.href = `/signup?email=${encryptedEmail}`
  };

  forceDemoRequest = encryptedEmail => {
    const { demotype } = this.props
    let demoUrl = `/contact/demo?email=${encryptedEmail}`
    if (demotype === 'personas') {
      demoUrl += '&personas'
    }
    if (demotype === 'protocols') {
      demoUrl += '&protocols'
    }
    location.href = demoUrl
  };

  handleEmailChange = e => {
    this.setState({
      email: e.target.value
    })
  };

  handleMarketoSubmit = () => {
    const formid = this.props.formid.toString()
    const { email } = this.state
    const context = {
      integrations: {
        Marketo: true
      }
    }
    const formType = 'Marketo'
    trackingPlan.formSubmitAttempt(
      {
        formid,
        form_fields: {},
        form_type: formType,
        email,
        company: '',
        munchkin_id: '',
        name: '',
        phone: ''
      },
      context,
      () => {
        trackingPlan.formSubmitSuccess(
          { formid, form_type: formType },
          context,
          () => {
            this.setState({
              submitted: true
            })
          }
        )
      }
    )
  };

  redirectOnSubmit = () => {
    const { redirect } = this.props
    location.href = redirect
  };

  getUserId = () => {
    if (this.props.userContext.user.id) {
      return this.props.userContext.user.id
    }
    if (
      typeof window.analytics.user === 'function' &&
      typeof window.analytics.user().id === 'function'
    ) {
      return window.analytics.user().id()
    }
    return ''
  };

  identifyAndTrack = () => {
    const url = window.location.toString()
    const { email } = this.state
    const userId = this.getUserId()
    const context = {
      integrations: {
        Marketo: true
      }
    }
    window.analytics.identify(userId, { email }, context, async() => {
      trackingPlan.leadCaptured(
        { email, url, location: window.location.href },
        context
      )
    })
  };

  academySubscription = () => {
    const formid = this.props.formid.toString()
    const { email } = this.state
    this.setState(
      {
        submitted: false
      },
      () => {
        window.analytics.identify(
          {
            email
          },
          () => {
            trackingPlan.academySubscribed({
              medium: 'email',
              email
            })
            trackingPlan.leadCaptured({
              email,
              url: document.location.href,
              location: 'Academy'
            })
            trackingPlan.formSubmitAttempt(
              {
                form_type: 'Academy Subscription',
                company: '',
                email,
                formid,
                form_fields: {},
                phone: '',
                name: '',
                munchkin_id: ''
              },
              undefined,
              () => {
                trackingPlan.formSubmitSuccess({
                  form_type: 'Academy',
                  formid
                })
              }
            )
            this.setState({
              submitted: true
            })
          }
        )
      }
    )
  };

  functionsLeadCapture = () => {
    const { email } = this.state

    trackingPlan.leadCaptured({
      email,
      location: 'Functions',
      url: location.href
    })
    this.setState({
      submitted: true
    })
  };

  handleSubmit = async e => {
    e.preventDefault()
    const { submitstyle } = this.props
    const { email } = this.state
    // to check if the email submitted is an mparticle email
    if (email.toLowerCase().includes('@mparticle')) {
      this.setState({
        submitted: true
      })
      return
    }
    this.identifyAndTrack()
    const encryptedEmail = encodeEmail(email)
    switch (submitstyle) {
      case 'marketo':
        this.handleMarketoSubmit()
        break
      case 'functions':
        this.functionsLeadCapture()
        break
      case 'Force Demo Request':
        this.forceDemoRequest(encryptedEmail)
        break
      case 'Force Signup':
        this.forceSignup(encryptedEmail)
        break
      case 'Academy Subscription':
        this.academySubscription()
        break
      case 'Redirect':
        this.redirectOnSubmit()
        break
      case 'Check for Business Email':
      default:
        this.checkForBusinessEmail(email, encryptedEmail)
        break
    }
  };

  isPersonalEmail(email) {
    const domain = email.split('@')[1]
    return personalEmailDomains.includes(domain)
  }

  shouldRenderNewEmailCapture = () => {
    const { theme } = this.props
    return ['Core', 'Personas Rebrand', 'Protocols Rebrand'].includes(
      theme.themeKey
    )
  };

  render() {
    const {
      label,
      className,
      placeholder,
      moduleId,
      style,
      submitmessage,
      theme
    } = this.props
    const { theme: _themeKey, ...rest } = this.props
    const { email, submitted } = this.state
    if (this.shouldRenderNewEmailCapture()) {
      return (
        <NewEmailCapture
          {...rest}
          {...this.state}
          handleSubmit={this.handleSubmit}
          handleEmailChange={this.handleEmailChange}
        />
      )
    }
    return (
      <Fragment>
        {!submitted && (
          <form
            className={cx(formStyle, className)}
            onSubmit={this.handleSubmit}
          >
            <input
              className={cx(inputStyle, {
                [lightInputStyle]: theme.footer.themeStyle === 'light'
              })}
              type="email"
              name="email"
              value={email}
              placeholder={placeholder}
              onChange={this.handleEmailChange}
              required
            />
            <Button
              className={buttonStyle}
              style={style}
              text={label}
              moduleId={moduleId}
            />
          </form>
        )}
        {submitted && <H5>{submitmessage}</H5>}
      </Fragment>
    )
  }
}

export default withTheme(withUserContext(EmailCapture))
