import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { ThemeProvider } from 'emotion-theming'
import { hot } from 'react-hot-loader/root'
import { cx } from 'emotion'
import StartupBanner from '@primitives/TopBanners/StartupBanner'
import PersonasBanner from '@primitives/TopBanners/PersonasBanner'
import ProtocolsBanner from '@primitives/TopBanners/ProtocolsBanner'
import SynapseBanner from '@primitives/TopBanners/SynapseBanner'
import PrivacyAndFunctionsBanner from '@primitives/TopBanners/PrivacyAndFunctionsBanner'

import FtfyHeader from './ftfy'
import LandingPageHeader from './landingPage'
import PublicSiteHeader from './publicSiteNew'
import StartupHeader from './startupHeader'
import SynapseHeader from './synapse'
import Synapse2019Header from './synapse2019'
import { bannerContainerStyle, scrolledBannerContainerStyle } from './style'

import { buildTheme } from '../../Style'
import { UserContextProvider } from '../../Context/UserContext'
import { logWarning } from '../../lib/Util'

class Headers extends Component {
  static HeaderMap = {
    ftfy: FtfyHeader,
    landing_page_navigation: LandingPageHeader,
    public_site_navigation: PublicSiteHeader,
    synapse_header: SynapseHeader,
    synapse_2019_header: Synapse2019Header,
    startup_navigation: StartupHeader
  };

  static TopBannersMap = {
    'Personas Banner': PersonasBanner,
    'Protocols Banner': ProtocolsBanner,
    'Startup Banner': StartupBanner,
    'Synapse Banner': SynapseBanner,
    'Privacy and Functions Banner': PrivacyAndFunctionsBanner
  };

  state = {
    heightLimit: 16,
    hidden: false,
    scrollTop: 0
  };

  componentDidMount() {
    document.addEventListener('scroll', this.onScroll, { passive: true })
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.onScroll)
  }

  onScroll = e => {
    const { top } = document.body.getBoundingClientRect()
    const { heightLimit } = this.state
    if (Math.abs(top) > heightLimit) {
      this.setState({
        hidden: true,
        scrollTop: top
      })
    } else {
      this.setState({
        hidden: false,
        scrollTop: top
      })
    }
  };

  renderBanner = () => {
    const { topBanner } = this.props.metadata
    const TopBannerElement = Headers.TopBannersMap[topBanner]
    if (!TopBannerElement) {
      return null
    }
    const { hidden } = this.state
    return <TopBannerElement hidden={hidden} />
  };

  render() {
    const { _layout, theme, ...rest } = this.props

    const Header = Headers.HeaderMap[_layout]
    if (!Header) {
      logWarning(
        `Headers could not find a match for ${_layout}, nothing will be rendered`
      )
      return null
    }
    // TODO: ADD THEME)
    const { hidden, scrollTop } = this.state
    const { theme: themeKey } = this.props.metadata
    const flags = {
      bannerHidden: hidden,
      hasBanner: Boolean(this.renderBanner())
    }

    return (
      <ThemeProvider theme={buildTheme(themeKey)}>
        <UserContextProvider>
          <div
            className={cx(bannerContainerStyle, {
              [scrolledBannerContainerStyle]: flags.bannerHidden
            })}
          >
            {this.renderBanner()}
            <Header {...rest} flags={flags} scrollTop={scrollTop} />
          </div>
        </UserContextProvider>
      </ThemeProvider>
    )
  }
}

Headers.propTypes = {
  _layout: PropTypes.string,
  layout: PropTypes.array,
  metadata: PropTypes.object,
  theme: PropTypes.string.isRequired
}

Headers.defaultProps = {
  _layout: 'none',
  layout: [],
  metadata: {}
}

export default hot(Headers)
