import { css } from 'emotion'

import {
  $xsMax,
  $submoduleXs,
  $submoduleMd,
  $mdMin,
  $smMax
} from '../../Style/variables'
import {
  $segmentGreen,
  $segmentGreenLighterShade,
  $segmentWhite,
  $segmentDarkGray
} from '../../Style/colors'
import { PublixTheme } from '../../Style/types'

export const formStyle = css`
  display: inline-flex;
  margin-bottom: ${$submoduleXs};

  margin-top: 2rem;
  @media only screen and (max-width: ${$xsMax}) {
    flex-wrap: wrap;
    width: 100%;
  }
`

export const inputStyle = css`
  background: ${$segmentWhite};
  border: solid 1px ${$segmentDarkGray};
  border-radius: 3px;
  font-size: 1rem;
  outline: none;
  padding: ${$submoduleXs} ${$submoduleMd};

  @media (min-width: ${$mdMin}) {
    min-width: 240px;
  }

  @media (max-width: ${$smMax}) {
    width: 100%;
  }

  @media only screen and (max-width: ${$xsMax}) {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    margin-left: auto;
    min-width: 170px;
    padding: ${$submoduleMd} ${$submoduleXs};
  }
`

export const lightInputStyle = css`
  border-width: 1px;
  border-color: ${$segmentGreen};
  &:hover {
    background: ${$segmentGreenLighterShade};
    text-decoration: none;
  }
`

export const buttonStyle = css`
  border-bottom-left-radius: 0px;
  border-top-left-radius: 0px;
  border-width: 3px;
  margin-left: -3px;

  &:not(:first-child) {
    margin-top: 0;
  }

  @media only screen and (max-width: ${$xsMax}) {
    border-bottom-left-radius: 4px;
    border-top-right-radius: 0px;
    margin-left: 0px;
    margin-right: auto;
    width: 100%;
  }
`

export const styledResponseMessageStyle = (theme: PublixTheme) => {
  return css`
    color: ${theme[theme.themeStyle].fonts.h5.color};
  `
}
