import { css } from 'emotion'

import { $segmentGreen, $segmentDark } from '../../Style/colors'

export const HamburgerStyle = css`
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  &:hover {
    cursor: pointer;
  }
  &:focus {
    outline: 0;
  }
  position: relative;
  z-index: 2;
`

export const barStyle = css`
  border-radius: 4px;
  height: 4px;
  position: relative;
  transition: all 350ms, background 150ms;
  width: 30px;
`

export const startupHamburgerStyle = css`
  transform: scale(0.75);
`

export const topBarStyle = css`
  animation: 0.5s topBarBurger;
  margin-bottom: 6px;
`

export const bottomBarStyle = css`
  animation: 0.5s bottomBarBurger;
  margin-top: 6px;
`

export const greenBarStyle = css`
  & > .bar {
    background: ${$segmentGreen};
  }
  &:active {
    & > .bar {
      background: ${$segmentGreen};
    }
  }
`

export const synapseBarStyle = css`
  & > .bar {
    background: #695ff7;
  }
  &:active {
    & > .bar {
      background: #695ff7;
    }
  }
`

export const whiteBarStyle = css`
  & > .bar {
    background: white;
  }
  &:active {
    & > .bar {
      background: white;
    }
  }
`

export const blueBarStyle = css`
  & > .bar {
    background: #5270f3;
  }
  &:active {
    & > .bar {
      background: #52bd95;
    }
  }
`

export const openTopBarStyle = css`
  animation: 0.5s topBarX;
  transform: translateY(10px) rotate(-45deg);
`

export const openMiddleBarStyle = css`
  opacity: 0;
`

export const openCenterBarStyle = css`
  animation: 0.5s bottomBarX;
  transform: translateY(-10px) rotate(45deg);
`

export const darkBarStyle = css`
  & > .bar {
    background: ${$segmentDark};
  }
`
