import React, { Component, Fragment, createRef } from 'react'
import { cx } from 'emotion'
import { withTheme } from 'emotion-theming'

import {
  hamburgerButtonContainerStyle,
  headerWrapperStyle,
  headerStyle,
  lightLogoStyle,
  linksContainerStyle,
  logoMarkStyle,
  pageLinksContainerStyle,
  scrolledStyle,
  pageLinksContainerOpenStyle,
  synapseLogoLinkStyle,
  navigationLinksContainerDesktopStyle,
  navigationLinksContainerMobileStyle
} from './style'
import { SynapseHeaderPropsInterface } from './types'
import Synaspse2019NavButtonList from './NavButtonList'

import Synapse2019RegistrationForm from '../../../Primitives/Synapse2019RegistrationForm'
import HamburgerButton from '../../../Primitives/HamburgerButton'

class Synapse2019Header extends Component<SynapseHeaderPropsInterface> {
  state = {
    formVisible: false,
    isOpen: false,
    isScrolled: false
  };

  modalRef = createRef<HTMLDivElement>();

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, { passive: true })
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    this.setState({
      isScrolled: window.scrollY >= 40
    })
  };

  onHamburgerClicked = () => {
    const { isOpen } = this.state

    this.setState({
      isOpen: !isOpen
    })
  };

  closeNav = cb => {
    this.setState(
      {
        isOpen: false
      },
      cb
    )
  };

  closeModal = () => {
    this.setState({ formVisible: false })
  };

  openModal = () => {
    this.setState({ formVisible: true })
  };

  render() {
    const { theme } = this.props
    const { formVisible, isOpen, isScrolled } = this.state
    const isDark = isScrolled || isOpen

    return (
      <Fragment>
        <header
          className={cx(headerWrapperStyle, {
            [scrolledStyle(theme)]: isDark
          })}
        >
          <div className={headerStyle}>
            <a href="/" className={synapseLogoLinkStyle}>
              <div
                className={cx(logoMarkStyle, {
                  [lightLogoStyle]: isDark
                })}
              />
            </a>
            <div className={linksContainerStyle}>
              <div className={navigationLinksContainerDesktopStyle}>
                <Synaspse2019NavButtonList
                  isDark={isDark}
                  onRequestInviteClicked={this.openModal}
                />
              </div>
              <div className={hamburgerButtonContainerStyle}>
                <HamburgerButton
                  isOpen={isOpen}
                  onHamburgerClicked={this.onHamburgerClicked}
                  style={isDark ? 'white' : 'synapse'}
                />
              </div>
            </div>
          </div>
        </header>
        <div
          className={cx(
            navigationLinksContainerMobileStyle,
            pageLinksContainerStyle(theme),
            {
              [pageLinksContainerOpenStyle]: isOpen
            }
          )}
        >
          <Synaspse2019NavButtonList
            isDark={isDark}
            onSpeakersLinkClicked={this.closeNav}
            onRequestInviteClicked={this.openModal}
          />
        </div>
        <Synapse2019RegistrationForm
          shouldShowForm={formVisible}
          onCloseModal={this.closeModal}
        />
      </Fragment>
    )
  }
}

export default withTheme(Synapse2019Header)
