import React, { Component, Fragment } from 'react'
import { ThemeProvider, withTheme } from 'emotion-theming'

import { DefaultTheme } from '@style'

import { PublixTheme } from '@style/types'
import Link from '@primitives/Link'
import P from '@primitives/Typography/P'
import Button from '@primitives/Button'

import {
  buttonContainerStyle,
  linkTextStyle,
  linkStyle,
  synapseHeaderButtonStyle,
  mobileAddressStyle
} from './style'
import { Synapse2019ButtonsProps } from './types'

class Synaspse2019NavButtonList extends Component<Synapse2019ButtonsProps> {
  static defaultProps = {
    theme: DefaultTheme
  };

  getUpdatedTheme = (ancestorTheme: PublixTheme): PublixTheme => {
    const { isDark } = this.props
    const themeStyle = isDark ? 'dark' : 'light'
    return {
      ...ancestorTheme,
      themeStyle
    }
  };

  onSpeakersLinkClicked = e => {
    const { onSpeakersLinkClicked } = this.props
    const nodeToScrollTo: any = document.querySelector('#speakers')
    if (!nodeToScrollTo) {
      return
    }
    if (onSpeakersLinkClicked) {
      onSpeakersLinkClicked(() => {
        const top = nodeToScrollTo.offsetTop
        window.scrollTo({
          top,
          behavior: 'smooth'
        })
      })
    } else {
      e.preventDefault()
      const top = nodeToScrollTo.offsetTop
      window.scrollTo({
        top,
        behavior: 'smooth'
      })
    }
  };

  render() {
    const { theme, onRequestInviteClicked } = this.props

    return (
      <ThemeProvider theme={this.getUpdatedTheme(theme)}>
        <Fragment>
          <Link
            minimalStyleUnderline={false}
            action="/speakers"
            className={linkStyle}
          >
            <span className={linkTextStyle}>Speakers</span>
          </Link>
          <Link
            minimalStyleUnderline={false}
            action="//downloads.ctfassets.net/9u2t3ap6pctb/5921uwef05ZLmPL3RrgnCk/948fdd2cbd33d272067e4782419648c8/Synapse_2019_Sponsorships.pdf"
            className={linkStyle}
          >
            <span className={linkTextStyle}>Sponsor</span>
          </Link>
          <Link
            minimalStyleUnderline={false}
            action="mailto:events@segment.com"
            className={linkStyle}
          >
            <span className={linkTextStyle}>Contact</span>
          </Link>
          <div className={buttonContainerStyle}>
            <Button
              className={synapseHeaderButtonStyle}
              style="primary"
              onClick={onRequestInviteClicked}
            >
              Get tickets
            </Button>
          </div>
          <div className={mobileAddressStyle}>
            <P>September 25-26, 2019</P>
            <P>SVN West, San Francisco</P>
          </div>
        </Fragment>
      </ThemeProvider>
    )
  }
}

export default withTheme(Synaspse2019NavButtonList)
