import React, { Component, Fragment } from 'react'
import { cx } from 'emotion'
import { withTheme } from 'emotion-theming'

import {
  headerWrapperStyle,
  headerStyle,
  linkTextStyle,
  linkStyle,
  logoMarkStyle,
  segmentLogoStyle,
  synapseHeaderButtonStyle
} from './style'
import { SynapseHeaderPropsInterface } from './types'

import Link from '../../../Primitives/Link'

class SynapseHeader extends Component<SynapseHeaderPropsInterface> {
  state = {
    shouldShowDifferentButton: false
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, { passive: true })
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    this.setState({ shouldShowDifferentButton: window.scrollY >= 300 })
  };

  render() {
    const { theme } = this.props
    const { shouldShowDifferentButton } = this.state
    return (
      <header className={cx(headerWrapperStyle(theme))}>
        <div className={headerStyle}>
          <Link action="/" className={linkStyle(theme)} style="none">
            {shouldShowDifferentButton ? (
              <div className={segmentLogoStyle} />
            ) : (
              <Fragment>
                <div className={logoMarkStyle} />
                <span className={linkTextStyle(theme)}>
                  Segment User Conference
                </span>
              </Fragment>
            )}
          </Link>
          <Link
            className={synapseHeaderButtonStyle}
            style="primary"
            action="/#request-an-invite"
          >
            Request an invite
          </Link>
        </div>
      </header>
    )
  }
}

export default withTheme(SynapseHeader)
