import React, { Component } from 'react'
import { withTheme, ThemeProvider } from 'emotion-theming'
import P from '@primitives/Typography/P'

import {
  subsciptionOverlayStyle,
  subscriptionContentContainerStyle,
  subsciptionOverlayTextStyle
} from './style'
import { SubscriptionOverlayPropTypesInterface } from './types'

import { emailCaptureStyle } from '../style'
import EmailCapture from '../../../../Primitives/EmailCapture'
import { PublixTheme } from '../../../../Style/types'

class SubscriptionOverlay extends Component<
  SubscriptionOverlayPropTypesInterface
> {
  getUpdatedTheme = (ancestorTheme: PublixTheme): PublixTheme => {
    return {
      ...ancestorTheme,
      themeStyle: 'dark'
    }
  };

  render() {
    return (
      <ThemeProvider theme={this.getUpdatedTheme}>
        <div className={subsciptionOverlayStyle}>
          <div className={subscriptionContentContainerStyle}>
            <P className={subsciptionOverlayTextStyle}>
              Subscribe to get a new lesson each week!
            </P>
            <EmailCapture
              style={'primary'}
              submitstyle={'Academy Subscription'}
              submitmessage={'Check your inbox for a confirmation email!'}
              className={emailCaptureStyle}
              label={'Send to Email'}
            />
          </div>
        </div>
      </ThemeProvider>
    )
  }
}

export default withTheme(SubscriptionOverlay)
