import * as React from 'react'
import { cx } from 'emotion'
import { withTheme } from 'emotion-theming'
import Button from '@primitives/Button'
import H5 from '@primitives/Typography/H5'

import { DefaultTheme } from '@style'

import { formStyle, inputStyle, buttonStyle } from './newStyle'
import { NewEmailCapturePropsInterface } from './types'

class NewEmailCapture extends React.Component<NewEmailCapturePropsInterface> {
  static defaultProps = {
    theme: DefaultTheme
  };

  render() {
    const {
      label,
      placeholder,
      moduleId,
      className,
      submitmessage,
      theme,
      email,
      submitted,
      handleSubmit,
      handleEmailChange
    } = this.props

    if (submitted) {
      return <H5>{submitmessage}</H5>
    }
    return (
      <form className={cx(formStyle, className)} onSubmit={handleSubmit}>
        <input
          className={inputStyle(theme)}
          onChange={handleEmailChange}
          value={email}
          name="email"
          type="email"
          placeholder={placeholder}
          required
        />
        <Button
          className={cx(buttonStyle(theme))}
          text={label}
          moduleId={moduleId}
        />
      </form>
    )
  }
}

export default withTheme(NewEmailCapture)
