import * as React from 'react'
import { withTheme } from 'emotion-theming'
import { cx } from 'emotion'

import { DefaultTheme } from '@style'

import { defaultStyle, firstModuleStyles } from './style'

import { TypographyPropsInterface } from '../types'
import { ModuleContextConsumer } from '../../../Context/ModuleContext'

class P extends React.Component<TypographyPropsInterface> {
  static defaultProps = {
    className: '',
    theme: DefaultTheme
  };

  render() {
    const { children, className, theme, ...rest } = this.props
    return (
      <ModuleContextConsumer>
        {({ isFirstModule }) => (
          <p
            className={cx(
              defaultStyle(theme),
              { [firstModuleStyles(theme)]: isFirstModule },
              className
            )}
            {...rest}
          >
            {children}
          </p>
        )}
      </ModuleContextConsumer>
    )
  }
}

export default withTheme(P)
