import merge from 'lodash.merge'

import { ModuleStyle, PartialModuleStyle } from './types'

import { ASSETS_PREFIX } from '../../../../config/client'

export const hexToRgbA = (hex, opacity = 1) => {
  let c
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('')
    if (c.length === 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]]
    }
    c = '0x' + c.join('')
    return (
      'rgba(' +
      [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') +
      `,${opacity})`
    )
  }
  throw new Error('Bad Hex')
}

export const assetUrl = url => `url(${ASSETS_PREFIX}/${url})`

export const newStyleMerger = (baseTheme: ModuleStyle) => {
  return (newTheme: PartialModuleStyle): ModuleStyle => {
    return merge({}, baseTheme, newTheme)
  }
}
