import * as React from 'react'
import { cx } from 'emotion'
import Icon from '@primitives/Icon'
import {
  useSpring,
  animated,
  useTransition,
  useChain,
  ReactSpringHook
} from 'react-spring'

import {
  dividerStyle,
  sectionItemLinkStyle,
  sectionItemTitleStyle,
  sectionItemSubtitle,
  subSectionStyle,
  iconContainerStyle,
  textContainerStyle,
  iconStyle,
  sectionCaretStyle,
  sectionContainerStyle,
  openSubsectionStyle,
  featureLinkStyle,
  featureTitleStyle,
  productTitleStyle,
  sectionItemTitleHoverStyles
} from './style'
import { DesktopNavigationSubSectionInterface } from './types'

const DesktopNavigationSubSection: React.SFC<
  DesktopNavigationSubSectionInterface
> = (props: DesktopNavigationSubSectionInterface) => {
  const springRef = React.useRef<ReactSpringHook>() as any
  const transitionRef = React.useRef<ReactSpringHook>() as any
  const { isOpen, sections } = props
  const style = useSpring({
    ref: springRef,
    pointerEvents: isOpen ? 'all' : 'none'
  })

  const transitions = useTransition(
    isOpen ? sections.links : [],
    item => `${item.href}-${item.title}`,
    {
      ref: transitionRef,
      unique: true,
      trail: 150 / sections.links.length,
      from: { opacity: 0, transform: `translateX(-1rem)` },
      enter: { opacity: 1, transform: `translateX(0)` },
      leave: { opacity: 0, transform: `translateX(-1rem)` }
    }
  )
  useChain(isOpen ? [springRef, transitionRef] : [transitionRef, springRef], [
    0,
    isOpen ? 0.1 : 0.2
  ])

  return (
    <animated.div
      className={cx(subSectionStyle, {
        [openSubsectionStyle]: isOpen
      })}
      style={style}
    >
      <div className={sectionCaretStyle} />
      <div className={sectionContainerStyle}>
        {transitions.map(({ item, key, props }) => {
          const {
            divider,
            feature,
            href,
            logo,
            product,
            subtitle,
            title
          } = item

          if (divider) {
            return <div key={key} className={dividerStyle} />
          }

          return (
            <animated.a
              key={key}
              href={href}
              className={cx(sectionItemLinkStyle, {
                [featureLinkStyle]: feature
              })}
              style={props}
            >
              <div className={iconContainerStyle}>
                <Icon
                  icon={logo}
                  style={18}
                  useDefaultFills={false}
                  className={iconStyle}
                />
                <span
                  className={cx(sectionItemTitleStyle, {
                    [featureTitleStyle]: feature,
                    [productTitleStyle]: product,
                    [sectionItemTitleHoverStyles]: true
                  })}
                >
                  {title}
                </span>
              </div>
              <div className={textContainerStyle}>
                {subtitle && <p className={sectionItemSubtitle}>{subtitle}</p>}
              </div>
            </animated.a>
          )
        })}
      </div>
    </animated.div>
  )
}

export default DesktopNavigationSubSection
