import * as React from 'react'
import { cx } from 'emotion'

import {
  ctaButtonsContainerStyle,
  navButtonStyle,
  navButtonAnchorLightBackgroundThemeStyle,
  navButtonAnchorDarkBackgroundStyle
} from './style'
import { DekstopAuthbButtonsInterface } from './types'

import {
  navLinkAnchorStyle,
  navLinkAnchorDarkBackgroundStyle,
  navLinkAnchorLightBackgroundThemeStyle
} from '../style'

class DekstopAuthbButtons extends React.Component<
  DekstopAuthbButtonsInterface
> {
  handleSignupClick = (e: any) => {
    const { pathname } = window.location
    const isHomepage = pathname === '/'

    window.analytics.track(`Navigation Signup Clicked`)
    if (isHomepage) {
      window.analytics.track(`Homepage Navigation Signup Clicked`)
    }
  };

  render() {
    const { shouldShowDemoButton, lightBackground } = this.props
    return (
      <ul className={ctaButtonsContainerStyle}>
        {shouldShowDemoButton ? (
          <a
            className={cx(navLinkAnchorStyle, {
              [navLinkAnchorDarkBackgroundStyle]: !lightBackground,
              [navLinkAnchorLightBackgroundThemeStyle]: lightBackground
            })}
            href="https://segment.com/contact/demo/"
          >
            Request a demo
          </a>
        ) : (
          <a
            className={cx(navLinkAnchorStyle, {
              [navLinkAnchorDarkBackgroundStyle]: !lightBackground,
              [navLinkAnchorLightBackgroundThemeStyle]: lightBackground
            })}
            href="https://segment.com/login/"
          >
            Log in
          </a>
        )}
        <a
          href="https://segment.com/signup/"
          className={cx(navButtonStyle, {
            [navButtonAnchorDarkBackgroundStyle]: !lightBackground,
            [navButtonAnchorLightBackgroundThemeStyle]: lightBackground
          })}
          onClick={this.handleSignupClick}
        >
          Sign up
        </a>
      </ul>
    )
  }
}

export default DekstopAuthbButtons
