import React, { Component, createRef } from 'react'
import { cx } from 'emotion'
import { ThemeProvider } from 'emotion-theming'

import {
  doesntHaveBannerStyle,
  hasBannerStyle,
  headerStyle,
  scrolledStyle,
  containerStyle
} from './style'
import DesktopNavigation from './DesktopNavigation'
import MobileNavigation from './MobileNavigation'
import {
  PublicHeaderPropTypesInterface,
  PublicHeaderStateInterface
} from './types'

import { buildTheme } from '@style'

class PublicSiteHeader extends Component<
  PublicHeaderPropTypesInterface,
  PublicHeaderStateInterface
> {
  private wrapperRef = createRef<HTMLDivElement>();

  static defaultProps = {
    flags: {
      bannerHidden: true,
      hasBanner: false
    },
    layout: []
  };

  constructor(props) {
    super(props)
    this.state = {
      isOpen: false,
      navigation: props.layout,
      activeNavItem: -1
    }
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.handleEventOutside)
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleEventOutside)
  }

  handleEventOutside = event => {
    const { wrapperRef } = this
    if (wrapperRef && !wrapperRef.current!.contains(event.target)) {
      this.toggleActiveNavItem(-1)
    }
  };

  toggleNavigation = () => {
    const { isOpen } = this.state
    this.setState({
      isOpen: !isOpen
    })
  };

  toggleActiveNavItem = idx => {
    const { activeNavItem } = this.state
    this.setState({
      activeNavItem: idx !== activeNavItem ? idx : -1
    })
  };

  render() {
    const { isOpen, activeNavItem, navigation } = this.state
    const { flags, metadata, scrollTop } = this.props
    const { hasBanner, bannerHidden } = flags
    const { firstModuleThemeStyle } = metadata
    const isFirstModuleDark =
      firstModuleThemeStyle === 'dark' || firstModuleThemeStyle === 'medium'
    const percentageScrolled = Math.max((64 - Math.abs(scrollTop)) / 64, 0)
    const lightBackground = !isFirstModuleDark || flags.bannerHidden
    return (
      <nav
        id="main-nav"
        className={cx(
          headerStyle,
          scrolledStyle(percentageScrolled),
          {
            [hasBannerStyle]: hasBanner,
            [doesntHaveBannerStyle]: bannerHidden
          },
          { open: isOpen }
        )}
        ref={this.wrapperRef}
      >
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1.0, viewport-fit=cover"
        />
        <ThemeProvider theme={buildTheme()}>
          <div className={containerStyle}>
            <DesktopNavigation
              lightBackground={lightBackground}
              navigation={navigation}
              shouldShowDemoButton={flags.bannerHidden}
              activeNavItem={activeNavItem}
              onNavItemClicked={this.toggleActiveNavItem}
            />
            <MobileNavigation
              lightBackground={lightBackground}
              hasBanner={hasBanner}
              navigation={navigation}
              activeNavItem={activeNavItem}
              onNavItemClicked={this.toggleActiveNavItem}
            />
          </div>
        </ThemeProvider>
      </nav>
    )
  }
}

export default PublicSiteHeader
