import { css } from 'emotion'
import { $lgDesktopMin, $tabletMax, $smDesktopMin } from '@style/variables'
import { PublixTheme } from '@style/types'

const time = 150

export const defaultSharedStyle = css`
  border-style: solid;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  border: none;
  &:hover {
    text-decoration: none;
  }
`

const joinButtonSeparationStyle = css`
  & + a,
  & + button {
    @media (min-width: ${$smDesktopMin}) {
      margin-left: 1.2em;
    }
    @media (max-width: ${$tabletMax}) {
      margin-top: 1em;
    }
  }
`

const fluidButtonStyle = (theme: PublixTheme) => css`
  @media (min-width: ${$lgDesktopMin}) {
    font-size: 1.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }
`

const sharedPrimaryAndSecondaryButtonsStyles = (theme: PublixTheme) => {
  const { buttons } = theme[theme.themeStyle]
  return css`
    border-radius: ${buttons._globals.cornerRadius};
    border-style: solid;
    cursor: pointer;
    display: inline-flex;
    font-size: ${buttons._globals.fontSize};
    outline: none;
    text-decoration: none;
    transition: background-color ${time}ms ease, border-color ${time}ms ease,
      color ${time}ms ease, box-shadow ${time}ms ease;
    white-space: nowrap;

    &:hover {
      text-decoration: none;
    }

    // So "Buttons" (non minimal links) are 100% on tablet and mobile
    @media (max-width: ${$tabletMax}) {
      justify-content: center;
      width: 100%;
    }
    ${joinButtonSeparationStyle};
  `
}

export const primaryButtonStyle = (theme: PublixTheme) => {
  const primaryButton = theme[theme.themeStyle].buttons.primary
  const { isFluid } = theme.moduleFlags
  return css`
    ${sharedPrimaryAndSecondaryButtonsStyles(theme)}
    ${isFluid && fluidButtonStyle(theme)}
    
    background-color: ${primaryButton.backgroundColor};
    border-color: ${primaryButton.borderColor};
    border-width: ${primaryButton.borderWidth};
    color: ${primaryButton.textColor};
    font-weight: ${primaryButton.fontWeight};
    padding: ${primaryButton.padding};
    text-transform: ${primaryButton.textTransform};

    &:hover {
      background-color: ${primaryButton.backgroundColorHover};
      border-color: ${primaryButton.borderColorHover};
      box-shadow: ${primaryButton.shadow};
      color: ${primaryButton.textColorHover};
    }
  `
}

export const secondaryButtonStyle = (theme: PublixTheme) => {
  const secondaryButton = theme[theme.themeStyle].buttons.secondary
  const { isFluid } = theme.moduleFlags
  return css`
    ${sharedPrimaryAndSecondaryButtonsStyles(theme)}
    ${isFluid && fluidButtonStyle(theme)}
    
    background-color: ${secondaryButton.backgroundColor};
    border-color: ${secondaryButton.borderColor};
    border-width: ${secondaryButton.borderWidth};
    color: ${secondaryButton.textColor};
    font-weight: ${secondaryButton.fontWeight};
    padding: ${secondaryButton.padding};
    text-transform: ${secondaryButton.textTransform};

    &:hover {
      background-color: ${secondaryButton.backgroundColorHover};
      border-color: ${secondaryButton.borderColorHover};
      box-shadow: ${secondaryButton.shadow};
      color: ${secondaryButton.textColorHover};
    }
  `
}

export const minimalButtonStyle = (theme: PublixTheme) => {
  const { minimal } = theme[theme.themeStyle].buttons
  return css`
    background-color: transparent;
    border: none;
    color: ${minimal.textColor};
    display: initial;
    padding: 0;
    position: relative;
    transition: background-color ${time}ms ease, border-color ${time}ms ease,
      color ${time}ms ease;

    &:hover {
      color: ${minimal.textColorHover};
    }

    a + & {
      display: inline-block;
    }
  `
}

export const minimalButtonUnderlineOnHoverStyle = (theme: PublixTheme) => {
  const { minimal } = theme[theme.themeStyle].buttons
  return css`
    @media (min-width: ${$smDesktopMin}) {
      &:after {
        bottom: 0.1em;
        content: "";
        display: block;
        height: 0.0625em;
        left: 0;
        position: absolute;
        transition: width ${time * 2}ms;
        width: 0;
        background: ${minimal.textUnderlineColor};
      }

      &:hover:after {
        width: 100%;
      }
    }

    @media (max-width: ${$tabletMax}) {
      &:hover {
        text-decoration: underline;
      }
    }

    h1 > &,
    h2 > &,
    h3 > &,
    h4 > &,
    h5 > &,
    h6 > & {
      &:hover {
        text-decoration: underline;
      }

      &:after {
        display: none;
      }
    }
  `
}

export const disabledButtonStyle = css`
  cursor: not-allowed;
`
