import { css } from 'emotion'
import { $mdDesktopMin } from '@style/variables'
import { $segmentLight } from '@style/colors'

const mobileTopbarHeight = 70

export const mobileGutterStyle = css`
  padding-left: 1.75rem;
  padding-right: 1.75rem;
  /* We detect if it' has support for Max/safe-area-inset-* keys so we can do cool things on phones with a notch (AKA IphoneX) */
  @media (orientation: landscape) {
    @supports (padding: max(0px)) {
      padding-left: env(safe-area-inset-left);
      padding-right: env(safe-area-inset-right);
    }
  }
`

export const containerStyle = css`
  @media (min-width: ${$mdDesktopMin}) {
    display: none;
  }
  width: 100%;
  height: ${mobileTopbarHeight}px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
`

export const logoStyle = css`
  align-self: center;
  height: 30px;
  width: 140px;
  svg {
    height: 30px;
    width: 140px;
  }
`

export const topbarContainerStyle = css`
  width: 100%;
  height: ${mobileTopbarHeight}px;
  min-height: ${mobileTopbarHeight}px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  z-index: 10;
  position: relative;
  transition: box-shadow 100ms ease-in-out;
`

export const scrolledShadowStyle = css`
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
`

export const navigationOpenContainerStyle = (hasBanner: boolean) => css`
  height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc((var(--vh, 1vh) * 100) - ${hasBanner ? `3.5rem` : `0px`});
  background-color: ${$segmentLight};
`

export const hamburgerButtonStyle = css`
  margin-right: 0;
`
