const PORT = process.env.PORT || 3000
const APP_ENV = process.env.APP_ENV || 'development'
const appName = 'publix'
const fullPreviewEnabled = APP_ENV !== 'production'
const isProd = APP_ENV !== 'development'
const isDev = !isProd
const enableSSR = isDev ? Boolean(process.env.ENABLE_SSR_ON_DEV) : true
const NODE_ENV = process.env.NODE_ENV || 'development'
const GOOGLE_MAPS_API = process.env.GOOGLE_MAPS_API
const GREENHOUSE_API_KEY = process.env.GREENHOUSE_API_KEY
const TAG = isProd ? process.env.TAG : 'dev'
const SENTRY_DSN = process.env.SENTRY_DSN
const IS_CLIENT_SIDE = process.env.IS_CLIENT_SIDE
const IS_SERVER_SIDE = !IS_CLIENT_SIDE
const CONTENTFUL_ACCESS_TOKEN = fullPreviewEnabled
  ? process.env.CONTENTFUL_ACCESS_TOKEN_PREVIEW
  : process.env.CONTENTFUL_ACCESS_TOKEN
const CONTENTS_IO_API_KEY = process.env.CONTENTS_IO_API_KEY
const CONTENTS_IO_API_URL_PREVIEW = process.env.CONTENTS_IO_API_URL_PREVIEW
const CONTENTS_IO_API_URL = process.env.CONTENTS_IO_API_URL
const CONTENTFUL_HOST = fullPreviewEnabled
  ? process.env.CONTENTFUL_HOST_PREVIEW
  : process.env.CONTENTFUL_HOST
const CONTENTFUL_SPACE_ID = process.env.CONTENTFUL_SPACE_ID
const ASSETS_PREFIX = (() => {
  if (APP_ENV === 'stage') {
    return `https://s3-us-west-2.amazonaws.com/publix-assets`
    // TODO: Change this to a CDN
  }
  if (APP_ENV === 'production') {
    return `https://s3-us-west-2.amazonaws.com/publix-assets-prod`
    // TODO: Change this to a CDN
  }
  return ''
})()

let APP_URL = `http://localhost:${PORT}`
if (APP_ENV === 'stage') {
  APP_URL = `https://segment.build`
}
if (APP_ENV === 'production') {
  APP_URL = `https://segment.com`
}

let PUBLIX_APP_URL = `http://localhost:${PORT}`
if (APP_ENV === 'stage') {
  PUBLIX_APP_URL = `https://publix.segment.build`
}
if (APP_ENV === 'production') {
  PUBLIX_APP_URL = `https://publix.segment.com`
}

module.exports = {
  appName,
  isProd,
  isDev,
  enableSSR,
  PORT,
  APP_URL,
  PUBLIX_APP_URL,
  TAG,
  IS_CLIENT_SIDE,
  APP_ENV,
  NODE_ENV,
  ASSETS_PREFIX,
  IS_SERVER_SIDE,
  CONTENTFUL_ACCESS_TOKEN,
  CONTENTFUL_HOST,
  CONTENTFUL_SPACE_ID,
  CONTENTS_IO_API_KEY,
  CONTENTS_IO_API_URL_PREVIEW,
  CONTENTS_IO_API_URL,
  GOOGLE_MAPS_API,
  GREENHOUSE_API_KEY,
  SENTRY_DSN
}
