import * as React from 'react'
import { withTheme } from 'emotion-theming'

import {
  IconPropsInterface,
  URLIconPropsInterface,
  SegmentIconPropsInterface,
  BluePrintJSIconPropsInterface
} from './types'
import SegmentIcon from './SegmentIcons'
import BlueprintIcon from './blueprint'

import { DefaultTheme } from '../../Style'
import { logWarning } from '../../lib/Util'

/**
 * This implementation is a rem`ix of the Icon component in Blueprintjs:
 * https://github.com/palantir/blueprint/blob/813e93f2/packages/core/src/components/icon/icon.tsx#L15
 * Refer to the LICENSE for BlueprintJS here: https://github.com/palantir/blueprint/blob/develop/LICENSE
 */

class Icon extends React.PureComponent<IconPropsInterface> {
  static defaultProps = {
    className: '',
    style: 'small',
    theme: DefaultTheme,
    useDefaultFills: true
  };

  static sizeMap = {
    small: 16,
    medium: 32,
    large: 48,
    xLarge: 64
  };

  static getSize = style => {
    if (typeof style === 'number') {
      return style
    }
    if (Icon.sizeMap[style]) {
      return Icon.sizeMap[style]
    }
    return Icon.sizeMap.small
  };

  renderSrcIcons = () => {
    const { src, className, style } = this.props as URLIconPropsInterface
    const size = Icon.getSize(style)
    return <img width={size} height={size} src={src} className={className} />
  };

  render() {
    const { src } = this.props as URLIconPropsInterface
    if (src) {
      const SrcIcon = () => this.renderSrcIcons()
      return <SrcIcon />
    }
    const { icon } = this.props as
      | SegmentIconPropsInterface
      | BluePrintJSIconPropsInterface
    if (!icon) {
      logWarning(
        `You tried to render a Icon. but an "icon" prop was either not provided or it was empty`
      )
      return null
    }
    if (icon.startsWith('icon-')) {
      return <SegmentIcon {...this.props as SegmentIconPropsInterface} />
    }
    return <BlueprintIcon {...this.props as BluePrintJSIconPropsInterface} />
  }
}

export default withTheme(Icon)
