import * as React from 'react'
import { IconSvgPaths16, IconSvgPaths20 } from '@blueprintjs/icons'

import Icon from './index'

import { BluePrintJSIconPropsInterface } from './types'

import { logWarning } from '../../lib/Util'

class BlueprintIcon extends React.Component<BluePrintJSIconPropsInterface> {
  render() {
    const { icon, style } = this.props
    const size = Icon.getSize(style)
    const svgPathsRecord =
      size === Icon.sizeMap.small ? IconSvgPaths16 : IconSvgPaths20
    const pathStrings = svgPathsRecord[icon]
    if (pathStrings == null) {
      logWarning(
        `You tried to render a Blueprint Icon. but "${icon}": is not a valid icon name`
      )
      return null
    }

    const { color, theme, title, className, useDefaultFills } = this.props
    // Choose which pixel grid is most appropriate for given icon size
    const pixelGridSize = size >= 16 ? 20 : 16
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`

    // blueprint has a 16px and 20px version of each icon, use 20px version if size of icon is greater than 20px
    const fills = Array(pathStrings.length).fill('')
    const fill = color ? { fill: color } : { fill: theme.themeColors.primary }
    const paths = pathStrings.map((d, i) => {
      const style = useDefaultFills ? { fill: fills[i] } : {}
      return <path key={i} d={d} style={style} fillRule="evenodd" />
    })

    return (
      <svg
        style={fill}
        data-icon={icon}
        width={size}
        height={size}
        viewBox={viewBox}
        className={className}
      >
        {title ? <title>{title}</title> : null}
        {paths}
      </svg>
    )
  }
}

export default BlueprintIcon
