import { AcademyTheme } from './Themes/academy'
import { CoreTheme } from './Themes/core'
import { EnterpriseTheme } from './Themes/enterprise'
import { FTFYTheme } from './Themes/ftfy'
import { JobsTheme } from './Themes/jobs'
import { PartnersTheme } from './Themes/partners'
import { PersonasTheme } from './Themes/personas'
import { PersonasRebrandTheme } from './Themes/personas-rebrand'
import { PrivacyTheme } from './Themes/privacy'
import { ProtocolsTheme } from './Themes/protocols'
import { ProtocolsRebrandTheme } from './Themes/protocols-rebrand'
import { SegmentTheme } from './Themes/segment'
import { StartupTheme } from './Themes/startup'
import { SynapseTheme } from './Themes/synapse'
import { Synapse2019Theme } from './Themes/synapse2019'
import { ThemeMapInterface, PublixTheme, AllowedThemes } from './types'

export const DefaultTheme = SegmentTheme

export const ThemeMap: ThemeMapInterface = {
  Academy: AcademyTheme,
  Core: CoreTheme,
  Enterprise: EnterpriseTheme,
  FTFY: FTFYTheme,
  Jobs: JobsTheme,
  Partners: PartnersTheme,
  Personas: PersonasTheme,
  'Personas Rebrand': PersonasRebrandTheme,
  Privacy: PrivacyTheme,
  Protocols: ProtocolsTheme,
  'Protocols Rebrand': ProtocolsRebrandTheme,
  Segment: SegmentTheme,
  Startup: StartupTheme,
  Synapse: SynapseTheme,
  'Synapse 2019': Synapse2019Theme
}

export const ThemeKeys = Object.keys(ThemeMap)

export const buildTheme = (name: AllowedThemes = 'Segment'): PublixTheme => {
  const theme = ThemeMap[name]
  if (!theme) {
    console.error(`Unrecognized theme ${theme}`)
    return DefaultTheme
  }
  return theme
}
