import * as React from 'react'
import ResizeObserver from 'resize-observer-polyfill'

export const useMeasureHook = () => {
  const ref = React.useRef()
  const [bounds, setBounds] = React.useState({
    left: 0,
    top: 0,
    width: 0,
    height: 0
  })
  const [resizeObserverInstance] = React.useState(
    () => new ResizeObserver(([entry]) => setBounds(entry.contentRect))
  )
  React.useEffect(() => {
    if (ref.current) {
      resizeObserverInstance.observe(ref.current!)
    }
    return () => resizeObserverInstance.disconnect()
  }, [])
  return [{ ref }, bounds]
}
