import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { cx } from 'emotion'

import {
  HamburgerStyle,
  barStyle,
  startupHamburgerStyle,
  greenBarStyle,
  blueBarStyle,
  whiteBarStyle,
  openTopBarStyle,
  openMiddleBarStyle,
  openCenterBarStyle,
  synapseBarStyle,
  topBarStyle,
  bottomBarStyle,
  darkBarStyle
} from './style'

class HamburgerButton extends Component {
  render() {
    const { isOpen, onHamburgerClicked, style, className } = this.props
    const isGreen = style === 'green'
    const isWhite = style === 'white'
    const isBlue = style === 'blue'
    const isSynapse = style === 'synapse'
    const isDark = style === 'dark'
    return (
      <div
        className={cx(
          HamburgerStyle,
          {
            [startupHamburgerStyle]: isBlue
          },
          {
            [greenBarStyle]: isGreen,
            [blueBarStyle]: isBlue,
            [whiteBarStyle]: isWhite,
            [synapseBarStyle]: isSynapse,
            [darkBarStyle]: isDark
          },
          className
        )}
        onClick={onHamburgerClicked}
      >
        <div
          className={cx('bar', barStyle, topBarStyle, {
            [openTopBarStyle]: isOpen
          })}
        />
        <div
          className={cx('bar', barStyle, {
            [openMiddleBarStyle]: isOpen
          })}
        />
        <div
          className={cx('bar', barStyle, bottomBarStyle, {
            [openCenterBarStyle]: isOpen
          })}
        />
      </div>
    )
  }
}

HamburgerButton.propTypes = {
  className: PropTypes.string,
  isOpen: PropTypes.bool.isRequired,
  onHamburgerClicked: PropTypes.func,
  style: PropTypes.string
}

HamburgerButton.defaultProps = {
  className: '',
  onHamburgerClicked: () => {},
  style: 'green'
}

export default HamburgerButton
