import { hot } from 'react-hot-loader/root'
import React, { Component } from 'react'
import { cx } from 'emotion'
import P from '@primitives/Typography/P'
import Link from '@primitives/Link'

import {
  bannerStyle,
  buttonStyle,
  textStyle,
  showOnDesktopStyle,
  showOnTabletStyle,
  showOnMobileStyle,
  hiddenStyle,
  hideOnMobileStyle
} from './style'

import { sharedStyle } from '../sharedStyle'

class SynapseBanner extends Component<{ hidden: boolean }> {
  static defaultProps = {
    hidden: false
  };

  render() {
    const { hidden } = this.props
    return (
      <Link
        className={cx(bannerStyle, sharedStyle, { [hiddenStyle]: hidden })}
        style="none"
        newtab=""
        action="https://synapse.segment.com"
      >
        <P className={textStyle}>
          <div className={showOnDesktopStyle}>
            Tickets on sale now for Synapse, Segment's annual user conference!
          </div>
          <div className={showOnTabletStyle}>
            Tickets available now for Synapse!
          </div>
        </P>
        <div className={cx(hideOnMobileStyle, buttonStyle)}>Get tickets</div>
        <div className={cx(showOnMobileStyle, buttonStyle)}>
          Get tickets for Synapse today
        </div>
      </Link>
    )
  }
}

export default hot(SynapseBanner)
