import * as React from 'react'
import { cx } from 'emotion'
import {
  useSpring,
  animated,
  useTransition,
  useChain,
  ReactSpringHook
} from 'react-spring'
import { useMeasureHook } from '@style/Animations/helpers'
import Icon from '@primitives/Icon'
import { $segmentDark } from '@style/colors'

import {
  sectionItemLinkStyle,
  sectionItemTitleStyle,
  sectionTitleContainerStyle,
  mobileLinksContainer,
  sectionItemSubtitle,
  featureLinkStyle
} from './style'
import { MobileLinksInterface } from './types'

const NavSubsection: React.SFC<MobileLinksInterface> = (
  props: MobileLinksInterface
) => {
  const springRef = React.useRef<ReactSpringHook>() as any
  const transitionRef = React.useRef<ReactSpringHook>() as any
  const { isOpen, sections } = props
  const [refToBind, elementBounds] = useMeasureHook()
  const { height: DomNodeHeight } = elementBounds as DOMRectReadOnly
  const style = useSpring({
    ref: springRef,
    height: isOpen ? DomNodeHeight : 0,
    opacity: isOpen ? 1 : 0
  })
  const transitions = useTransition(
    isOpen ? sections : [],
    item => `${item.href}-${item.title}`,
    {
      ref: transitionRef,
      unique: true,
      trail: 150 / sections.length,
      from: { opacity: 0, transform: `translateX(-1rem)` },
      enter: { opacity: 1, transform: `translateX(0)` },
      leave: { opacity: 0, transform: `translateX(-1rem)` }
    }
  )
  useChain(isOpen ? [springRef, transitionRef] : [transitionRef, springRef], [
    0,
    isOpen ? 0.1 : 0.3
  ])

  return (
    <div className={mobileLinksContainer}>
      <animated.div style={style}>
        <div {...refToBind as any}>
          {transitions.map(({ item, key, props }) => {
            const sectionLink = item
            return (
              <animated.a
                key={key}
                className={cx(sectionItemLinkStyle, {
                  [featureLinkStyle]: sectionLink.feature
                })}
                href={sectionLink.href}
                style={props}
              >
                <div className={sectionTitleContainerStyle}>
                  <Icon
                    icon={sectionLink.logo}
                    style={16}
                    color={$segmentDark}
                  />
                  <span className={sectionItemTitleStyle}>
                    {sectionLink.title}
                  </span>
                </div>
                <div
                // className={textContainerStyle}
                >
                  {sectionLink.subtitle && (
                    <p className={sectionItemSubtitle}>
                      {sectionLink.subtitle}
                    </p>
                  )}
                </div>
              </animated.a>
            )
          })}
        </div>
      </animated.div>
    </div>
  )
}

export default NavSubsection
