import { hot } from 'react-hot-loader/root'
import React, { Component } from 'react'
import { cx } from 'emotion'
import P from '@primitives/Typography/P'
import Link from '@primitives/Link'

import {
  bannerStyle,
  buttonStyle,
  textStyle,
  showOnDesktopStyle,
  showOnTabletStyle,
  hiddenStyle,
  hideOnMobileStyle
} from './style'

import { sharedStyle } from '../sharedStyle'

class PrivacyAndFunctionsBanner extends Component<{ hidden: boolean }> {
  static defaultProps = {
    hidden: false
  };

  render() {
    const { hidden } = this.props
    return (
      <Link
        className={cx(bannerStyle, sharedStyle, { [hiddenStyle]: hidden })}
        style="none"
        newtab=""
        action="https://segment.com/blog/categories/product/"
      >
        <P className={textStyle}>
          <div className={showOnDesktopStyle}>
            New from Segment: Privacy Portal and Functions
          </div>
          <div className={cx(showOnTabletStyle)}>
            New from Segment: <br /> Privacy Portal and Functions
          </div>
        </P>
        <div className={cx(hideOnMobileStyle, buttonStyle)}>Learn more</div>
      </Link>
    )
  }
}

export default hot(PrivacyAndFunctionsBanner)
