import React, { Component } from 'react'
import { cx } from 'emotion'
import { withTheme } from 'emotion-theming'

import { DefaultTheme } from '@style'

import { ButtonPropsInterface } from './types'
import { defaultButtonStyle } from './style'

import { trackingPlan } from '../../lib/tracking-plan'
import {
  defaultSharedStyle,
  primaryButtonStyle,
  secondaryButtonStyle,
  minimalButtonStyle,
  disabledButtonStyle,
  minimalButtonUnderlineOnHoverStyle
} from '../Link/sharedStyles'

class Button extends Component<ButtonPropsInterface> {
  static defaultProps = {
    className: '',
    disabled: false,
    isInline: false,
    moduleId: 'NO_MODULE_ID',
    onClick: () => {},
    style: 'primary',
    text: '',
    theme: DefaultTheme
  };

  getIsPrimary = () => {
    const { style } = this.props
    if (!style) {
      return false
    }
    return style.toLowerCase().includes('primary')
  };

  getIsSecondary = () => {
    const { style } = this.props
    if (!style) {
      return false
    }
    return style.toLowerCase().includes('secondary')
  };

  getIsMinimal = () => {
    if (!this.getIsSecondary() && !this.getIsPrimary()) {
      return true
    }
    return false
  };

  handleClick = () => {
    const { children, moduleId, onClick, text, type } = this.props
    // TODO: Remove this "Inner Text" thing. If it's a "children" It should extract the value differently.
    const innerText = text.length ? text : children ? children.toString() : ''
    trackingPlan.buttonClicked({
      buttonAction: type,
      innerText,
      moduleId
    })
    onClick()
  };

  render() {
    const { text, className, theme, children, type, disabled } = this.props
    return (
      <button
        className={cx(
          defaultSharedStyle,
          defaultButtonStyle,
          {
            [primaryButtonStyle(theme)]: this.getIsPrimary(),
            [secondaryButtonStyle(theme)]: this.getIsSecondary(),
            [minimalButtonStyle(theme)]: this.getIsMinimal(),
            [minimalButtonUnderlineOnHoverStyle(theme)]: this.getIsMinimal(),
            [disabledButtonStyle]: disabled
          },
          className
        )}
        onClick={this.handleClick}
        type={type}
        disabled={disabled}
      >
        <span>{text || children}</span>
      </button>
    )
  }
}

export default withTheme(Button)
