import React, { Component } from 'react'
import { cx } from 'emotion'
import { withTheme } from 'emotion-theming'
import Url from 'url-parse'
import get from 'lodash.get'

import { DefaultTheme } from '@style'

import {
  defaultSharedStyle,
  primaryButtonStyle,
  secondaryButtonStyle,
  minimalButtonStyle,
  minimalButtonUnderlineOnHoverStyle
} from './sharedStyles'
import { LinkPropsInterface, LinkStateInterface } from './types'

import { trackingPlan } from '../../lib/tracking-plan'

class Link extends Component<LinkPropsInterface, LinkStateInterface> {
  static defaultProps = {
    action: '#',
    className: '',
    forwardqueryparams: false,
    minimalStyleUnderline: true,
    moduleId: 'NO_MODULE_ID',
    newtab: false,
    onClick: null,
    style: null,
    textClassName: '',
    theme: DefaultTheme
  };

  constructor(props) {
    super(props)
    this.state = {
      href: props.action,
      target: '_self'
    }
  }

  componentDidMount() {
    const { href } = this.state
    const shouldForwardQueryParams = this.shouldForwardQueryParams()
    if (
      shouldForwardQueryParams &&
      window &&
      window.location &&
      window.location.pathname.includes('industry/startups') &&
      window.location.search
    ) {
      this.setState({
        href: `${href}${window.location.search}`
      })
    }
    if (window && window.location.hostname) {
      this.setTarget(window.location.hostname)
    }
  }

  getIsPrimary = () => {
    const { style } = this.props
    if (!style) {
      return false
    }
    return style.toLowerCase().includes('primary')
  };

  getIsSecondary = () => {
    const { style } = this.props
    if (!style) {
      return false
    }
    return style.toLowerCase().includes('secondary')
  };

  getIsMinimal = () => {
    if (!this.getIsSecondary() && !this.getIsPrimary()) {
      return true
    }
    const { style } = this.props
    if (!style) {
      return true
    }
    return style.toLowerCase().includes('minimal')
  };

  shouldForwardQueryParams = () => {
    const { forwardqueryparams } = this.props
    if (forwardqueryparams === '') {
      return true
    }
    return forwardqueryparams
  };

  getTarget = currentHostname => {
    const { newtab, target } = this.props
    const { href } = this.state
    // If "target" prop is set, then we just bypass all logic and return that
    if (target) {
      return target
    }
    // default all external links to open in a new tab
    try {
      if (typeof newtab === 'string' || newtab) {
        return '_blank'
      }
      const { hostname } = new Url(href)
      if (
        !hostname.includes('segment') &&
        !hostname.includes('fixedthatforyou') &&
        !hostname.includes(currentHostname)
      ) {
        return '_blank'
      }
    } catch (e) {}
    return '_self'
  };

  setTarget = hostname => {
    const target = this.getTarget(hostname)
    this.setState({ target })
  };

  getLinkThemeStyles = () => {
    if (this.props.style === 'none') {
      return {}
    }
    const { theme, minimalStyleUnderline } = this.props
    return cx({
      [primaryButtonStyle(theme)]: this.getIsPrimary(),
      [secondaryButtonStyle(theme)]: this.getIsSecondary(),
      [minimalButtonStyle(theme)]: this.getIsMinimal(),
      [minimalButtonUnderlineOnHoverStyle(theme)]:
        this.getIsMinimal() && minimalStyleUnderline
    })
  };

  handleClick = e => {
    const { href } = this.state
    const { moduleId, children, onClick } = this.props
    const innerText = get(children, 'props.content', '')
    trackingPlan.linkClicked({
      destinationHref: href,
      innerText,
      moduleId
    })
    if (onClick && typeof onClick === 'function') {
      onClick(e)
    }
  };

  render() {
    const { href, target } = this.state
    const { className, children } = this.props
    const themeStyles = this.getLinkThemeStyles()
    return (
      <a
        className={cx(defaultSharedStyle, themeStyles, className)}
        href={href}
        onClick={this.handleClick}
        target={target}
      >
        {children}
      </a>
    )
  }
}

export default withTheme(Link)
