import { css } from 'emotion'
import {
  $mdDesktopContainer,
  $mdDesktopMin,
  $smDesktopMax,
  $lgDesktopMin,
  $lgDesktopContainer
} from '@style/variables'
import { $segmentWhite, $segmentDark } from '@style/colors'
import { PublixTheme } from '@style/types'

export const headerHeight = '5rem'

const transitionStyle = `250ms ease`

export const bigScreenNavContainerStyle = css`
  display: none;
  @media (min-width: ${$mdDesktopMin}) {
    height: ${headerHeight};
    display: flex;
    flex-direction: row;
    padding: 0;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    width: ${$mdDesktopContainer};
    padding-left: 5vw;
    padding-right: 5vw;
    justify-content: space-around;
  }
  @media (min-width: ${$lgDesktopMin}) {
    width: ${$lgDesktopContainer};
    padding-right: 5vw;
    padding-left: 5vw;
  }
`

export const navLinkStyle = css`
  margin: 0 !important;
  justify-content: center;
  list-style: none;
  display: inline-flex;
  text-decoration: none;
  position: relative;
`

export const sectionLinkContainerStyle = css`
  position: relative;
  display: inherit;
  margin-right: 2rem;
  &:last-of-type {
    margin-right: 0;
  }
  @media (max-width: ${$smDesktopMax}) {
    margin-right: 1.5rem;
    &:last-of-type {
      margin-right: 0.5rem;
    }
  }
`

export const chevronIconStyle = css`
  & path {
    transition: fill ${transitionStyle};
  }
`

export const chevronIconDarkBackgroundStyle = css`
  & path {
    fill: ${$segmentWhite} !important;
  }
`

export const chevronIconLightBackgroundStyle = css`
  & path {
    fill: ${$segmentDark} !important;
  }
`

export const opacityTransitionsStyle = css`
  transition: opacity ${transitionStyle};
`

export const navLinkContainerStyle = css`
  display: flex;
  flex: 1;
  max-width: 45rem;
  margin: 0;
  padding-left: 2rem;
  justify-content: flex-start;
  &:hover .${navLinkStyle} .${opacityTransitionsStyle} {
    opacity: 0.5;
  }
  & .${navLinkStyle}:hover .${opacityTransitionsStyle} {
    opacity: 0.9;
  }
`

export const navLinkAnchorStyle = css`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: color ${transitionStyle};
  text-decoration: none;
  cursor: pointer;
`

export const navLinkAnchorDarkBackgroundStyle = css`
  color: ${$segmentWhite};
`

export const navLinkAnchorLightBackgroundThemeStyle = css`
  color: ${$segmentDark};
`

export const iconContainerStyle = css`
  display: flex;
  transition: transform ${transitionStyle};
  transform: rotate(0deg);
  margin-top: 0.25em;
  margin-left: 0.1em;
`

export const openIconContainerStyle = css`
  transform: rotate(180deg);
`

export const subSectionTitle = (theme: PublixTheme) => css`
  color: ${theme.themeColors.text};
  font-size: 1rem;
  font-weight: 900;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
`

export const sectionItemTitleStyle = (theme: PublixTheme) => css`
  color: ${$segmentWhite};
  font-size: 1rem;
`

export const sectionItemSubtitle = (theme: PublixTheme) => css`
  color: ${theme.themeColors.text};
  font-size: 0.75em;
  padding: 0;
  margin: 0;
`

export const logoAndLinksContainerStyle = css`
  display: inline-flex;
  flex: 1;
  flex-direction: row;
`

export const logoContainerStyle = css`
  display: inline-flex;
  height: 60px;
  align-items: center;
  overflow: hidden;
`

export const desktopNavigationWrapperStyle = css`
  position: relative;
`
