import { css } from 'emotion'
import { PublixTheme } from '@style/types'
import {
  $mobileMax,
  $tabletMin,
  $tabletMax,
  $smDesktopMin,
  $smDesktopMax,
  $mdDesktopMin,
  $mdDesktopMax,
  $lgDesktopMin
} from '@style/variables'

export const defaultStyle = (theme: PublixTheme) => {
  const { bodyModules, bodyFont, normal } = theme.fonts

  return css`
    color: ${theme[theme.themeStyle].fonts.p.color};
    font-family: ${bodyFont};
    font-weight: ${normal};
    letter-spacing: ${bodyModules.p.letterSpacing};
    line-height: ${bodyModules.p.lineHeight};
    margin: 0;

    &:not(:first-child) {
      margin-top: ${bodyModules.p.marginTop};
    }

    @media (min-width: ${$lgDesktopMin}) {
      font-size: ${bodyModules.p.yugeDesktop};
    }
    @media (max-width: ${$mdDesktopMax}) and (min-width: ${$mdDesktopMin}) {
      font-size: ${bodyModules.p.largeDesktop};
    }
    @media (max-width: ${$smDesktopMax}) and (min-width: ${$smDesktopMin}) {
      font-size: ${bodyModules.p.smallDesktop};
    }
    @media (max-width: ${$tabletMax}) and (min-width: ${$tabletMin}) {
      font-size: ${bodyModules.p.tablet};
    }
    @media (max-width: ${$mobileMax}) {
      font-size: ${bodyModules.p.mobile};
    }
  `
}

export const firstModuleStyles = (theme: PublixTheme) => {
  const {
    yugeDesktop,
    largeDesktop,
    smallDesktop,
    tablet,
    mobile
  } = theme.fonts.heroModule.p

  return css`
    @media (min-width: ${$lgDesktopMin}) {
      font-size: ${yugeDesktop};
    }
    @media (max-width: ${$mdDesktopMax}) and (min-width: ${$mdDesktopMin}) {
      font-size: ${largeDesktop};
    }
    @media (max-width: ${$smDesktopMax}) and (min-width: ${$smDesktopMin}) {
      font-size: ${smallDesktop};
    }
    @media (max-width: ${$tabletMax}) and (min-width: ${$tabletMin}) {
      font-size: ${tablet};
    }
    @media (max-width: ${$mobileMax}) {
      font-size: ${mobile};
    }
  `
}
