import React, { Component } from 'react'
import { cx } from 'emotion'

import {
  authButtonsContainerStyle,
  authButtonsWrapperStyle,
  buttonContainerStyle,
  mobileAuthButtonStyle
} from './style'

import {
  navButtonStyle,
  navButtonAnchorLightBackgroundThemeStyle
} from '../../DesktopNavigation/AuthButtons/style'
import {
  navLinkAnchorStyle,
  navLinkAnchorLightBackgroundThemeStyle
} from '../../DesktopNavigation/style'

class InlineAuthButtons extends Component {
  handleSignupClick = (e: any) => {
    const { pathname } = window.location
    const isHomepage = pathname === '/'

    window.analytics.track(`Navigation Signup Clicked`)
    if (isHomepage) {
      window.analytics.track(`Homepage Navigation Signup Clicked`)
    }
  };

  render() {
    return (
      <ul className={authButtonsContainerStyle}>
        <div className={authButtonsWrapperStyle}>
          <div className={buttonContainerStyle}>
            <a
              href="https://segment.com/signup/"
              className={cx(
                navButtonStyle,
                navButtonAnchorLightBackgroundThemeStyle,
                mobileAuthButtonStyle
              )}
              onClick={this.handleSignupClick}
            >
              Sign up
            </a>
          </div>
          <div className={buttonContainerStyle}>
            <a
              className={cx(
                navLinkAnchorStyle,
                navLinkAnchorLightBackgroundThemeStyle,
                mobileAuthButtonStyle
              )}
              href="https://segment.com/login/"
            >
              Log in
            </a>
          </div>
        </div>
      </ul>
    )
  }
}

export default InlineAuthButtons
