import * as React from 'react'

import { SegmentIconSvgPaths } from './segmentIconSvgPaths'

import { SegmentIconPropsInterface } from '../types'
import { logWarning } from '../../../lib/Util'
import Icon from '../'

class SegmentIcons extends React.Component<SegmentIconPropsInterface> {
  render() {
    const { icon } = this.props
    const name = icon.substring(5)
    if (!SegmentIconSvgPaths[name]) {
      logWarning(
        `You tried to render a Segment Icon. but "${icon}": is not a valid icon name`
      )
      return null
    }
    const svg = {
      pathStrings: SegmentIconSvgPaths[name].pathStrings,
      viewBox: SegmentIconSvgPaths[name].viewBox,
      fills: SegmentIconSvgPaths[name].fills
    }
    const { pathStrings, viewBox, fills } = svg
    const {
      color,
      theme,
      title,
      className,
      useDefaultFills,
      style
    } = this.props
    const size = Icon.getSize(style)
    const fill = useDefaultFills
      ? color
        ? { fill: color }
        : { fill: theme.themeColors.primary }
      : {}
    const paths = pathStrings.map((d, i) => {
      const style = useDefaultFills ? { fill: fills[i] } : {}
      return <path key={i} d={d} style={style} fillRule="evenodd" />
    })

    return (
      <svg
        style={fill}
        data-icon={icon}
        width={size}
        height={size}
        viewBox={viewBox}
        className={className}
      >
        {title ? <title>{title}</title> : null}
        {paths}
      </svg>
    )
  }
}

export default SegmentIcons
