import React from 'react'
import { render } from 'react-dom'
import { hydrate as emotionHydrate } from 'emotion'

import Headers from './index'

import { decode } from '../../lib/Util'
import { startRaven, ErrorBoundary } from '../../lib/errorTracking'

const navElement = document.querySelector('#app-nav [publix]')
const { data, emotionIds } = decode(window.__NAV_DATA__)
const metadata = decode(window.__METADATA__)
startRaven()
emotionHydrate(emotionIds)
render(
  <ErrorBoundary>
    <Headers {...data} metadata={metadata} />
  </ErrorBoundary>,
  navElement
)
