import React, { Component } from 'react'

import { ModuleContextType, WithModuleContextProps } from './types'

import { getDisplayName } from '../helpers'

const defaultState: ModuleContextType = {
  moduleIndex: -1,
  isFirstModule: false
}

const { Provider, Consumer } = React.createContext<ModuleContextType>(
  defaultState
)

export const ModuleContextConsumer = Consumer

export const withModuleContext = <P extends object>(
  Component: React.ComponentType<P & WithModuleContextProps>
) => {
  return class WithModuleContext extends React.Component<P> {
    static displayName = `WithModuleContext(${getDisplayName(Component)})`;
    render() {
      return (
        <ModuleContextConsumer>
          {data => <Component {...this.props as P} moduleContext={data} />}
        </ModuleContextConsumer>
      )
    }
  }
}

export class ModuleContextProvider extends Component<
  any,
  { value: ModuleContextType }
> {
  state = {
    value: defaultState
  };

  componentDidMount() {
    const { index } = this.props
    this.setState({
      value: {
        moduleIndex: index + 1,
        isFirstModule: index === 0
      }
    })
  }

  render() {
    return <Provider value={this.state.value}>{this.props.children}</Provider>
  }
}
