import React, { Component } from 'react'

import { UserDataType, WithUserContextProps } from './types'

import { getDisplayName } from '../helpers'
import { decode, getCookies } from '../../lib/Util'

const defaultState: UserDataType = {
  isLoggedIn: false,
  loaded: false,
  user: {
    email: '',
    id: '',
    name: ''
  }
}

const { Provider, Consumer } = React.createContext<UserDataType>(defaultState)

export const UserContextConsumer = Consumer

export const withUserContext = <P extends object>(
  Component: React.ComponentType<P & WithUserContextProps>
) => {
  return class WithUserContext extends React.Component<P> {
    static displayName = `WithUserContext(${getDisplayName(Component)})`;
    render() {
      return (
        <UserContextConsumer>
          {data => <Component {...this.props as P} userContext={data} />}
        </UserContextConsumer>
      )
    }
  }
}

export class UserContextProvider extends Component<
  any,
  { value: UserDataType }
> {
  state = {
    value: defaultState
  };

  isLoaded = () => {
    this.setState({
      value: { ...this.state.value, loaded: true }
    })
  };

  componentDidMount() {
    try {
      if (window && window.__USER_DATA__) {
        const data = decode(window.__USER_DATA__)
        const { isLoggedIn, ...userData } = data
        const user = {
          ...this.state.value.user,
          ...userData
        }
        // If no USER ID is present, try to fill with server-side-provided user Id
        if (!user.id) {
          const { ajs_user_id: AjsUserId } = getCookies()
          if (AjsUserId) {
            user.id = AjsUserId
          }
        }
        this.setState(
          {
            value: {
              ...this.state.value,
              user,
              isLoggedIn
            }
          },
          this.isLoaded
        )
      }
    } catch (e) {
      this.isLoaded()
    }
  }

  render() {
    return <Provider value={this.state.value}>{this.props.children}</Provider>
  }
}
