export const decodeEmail = hash => {
  try {
    return atob(decodeURIComponent(hash))
  } catch (e) {}
  return hash
}

export const encodeEmail = email => {
  if (email && email.includes('@')) {
    return btoa(email)
  }
  return email
}

export const humanReadable = stringToReplace => {
  const rex = /([A-Z])([A-Z])([a-z])|([a-z])([A-Z])/g
  return stringToReplace.replace(rex, '$1$4 $2$3$5')
}

const removeLast = (s: string) => s.slice(0, s.length - 1)

const removeFirst = (s: string) => s.slice(1)

export const linkTypeToLayoutString = linkType => {
  return linkType.replace(/([A-Z])/g, $1 => {
    return '_' + $1.toLowerCase()
  })
}

export const removeLeadingAndTrailingSlash = (path = '') => {
  const SLASH = '/'
  let newPath = path
  if (newPath.startsWith(SLASH)) {
    newPath = removeFirst(newPath)
  }
  if (newPath.endsWith(SLASH)) {
    newPath = removeLast(newPath)
  }
  return newPath
}

export const decode = data => JSON.parse(decodeURIComponent(window.atob(data)))

// receives and object and an array of keys, and creates a new object with only the keys defined on the array
export const filterObject = (baseObject, allowedKeys) => {
  return Object.keys(baseObject)
    .filter(key => allowedKeys.includes(key))
    .reduce((obj, key) => {
      obj[key] = baseObject[key]
      return obj
    }, {})
}

export const getCookies = (): any => {
  return document.cookie.split(';').reduce((res, c) => {
    const [key, val] = c
      .trim()
      .split('=')
      .map(decodeURIComponent)
    const allNumbers = str => /^\d+$/.test(str)
    try {
      return Object.assign(res, {
        [key]: allNumbers(val) ? val : JSON.parse(val)
      })
    } catch (e) {
      return Object.assign(res, { [key]: val })
    }
  }, {})
}

export const logWarning = (...any) => {
  if (process.env.NODE_ENV !== 'production') {
    console.warn(...any)
  }
}

export const logError = (...any) => {
  if (process.env.NODE_ENV !== 'production') {
    console.error(...any)
  }
}

export const sleep = ms => new Promise(resolve => setTimeout(resolve, ms))

export const getHeightOfLargestOfSetOfRefs = (listOfRefs: Set<HTMLElement>) => {
  if (!listOfRefs.size) {
    return 0
  }
  return Array.from(listOfRefs).reduce((prev, current) => {
    if (!current || !current.getBoundingClientRect) {
      return
    }
    const rects = current.getBoundingClientRect()
    const { height } = rects
    if (height > prev) {
      return height
    } else {
      return prev
    }
  }, 0)
}
