import * as React from 'react'
import { cx } from 'emotion'
import Icon from '@primitives/Icon'
import {
  useSpring,
  animated,
  useTransition,
  useChain,
  ReactSpringHook
} from 'react-spring'

import {
  sectionItemLinkStyle,
  sectionItemTitleStyle,
  sectionItemSubtitle,
  subSectionStyle,
  iconContainerStyle,
  textContainerStyle,
  iconStyle,
  sectionCaretStyle,
  sectionContainerStyle,
  openSubsectionStyle,
  columnStyle,
  columnTitleStyle
} from './style'
import { DesktopNavigationSubSectionInterface } from './types'

const DesktopNavigationSubSection: React.SFC<
  DesktopNavigationSubSectionInterface
> = (props: DesktopNavigationSubSectionInterface) => {
  const springRef = React.useRef<ReactSpringHook>() as any
  const { isOpen, columns } = props
  const transitionRefs: any[] = []
  Array(columns.length).forEach(el => {
    transitionRefs.push(React.useRef<ReactSpringHook>() as any)
  })
  const style = useSpring({
    ref: springRef,
    pointerEvents: isOpen ? 'all' : 'none'
  })
  const sectionTransitions: any[] = []
  columns.forEach((sectionColumn, index: number) => {
    const ref = transitionRefs[index]
    sectionTransitions.push(
      useTransition(
        isOpen ? sectionColumn.links : [],
        item => `${item.href}-${item.title}`,
        {
          ref,
          unique: true,
          trail: 150 / sectionColumn.links.length,
          from: { opacity: 0, transform: `translateX(-1rem)` },
          enter: { opacity: 1, transform: `translateX(0)` },
          leave: { opacity: 0, transform: `translateX(-1rem)` }
        }
      )
    )
  })
  useChain(
    isOpen ? [springRef, ...transitionRefs] : [...transitionRefs, springRef],
    [0, isOpen ? 0.1 : 0.2]
  )

  return (
    <animated.div
      className={cx(subSectionStyle, {
        [openSubsectionStyle]: isOpen
      })}
      style={style}
    >
      <div className={sectionCaretStyle} />
      <div className={cx(sectionContainerStyle)}>
        {sectionTransitions.map((transitions, index) => {
          const columnTitle = columns[index].title
          return (
            <div className={columnStyle} key={index}>
              {columnTitle && (
                <div className={columnTitleStyle}>{columnTitle}</div>
              )}
              {transitions.map(({ item, key, props }) => {
                const sectionLink = item
                return (
                  <animated.a
                    key={key}
                    href={sectionLink.href}
                    className={sectionItemLinkStyle}
                    style={props}
                  >
                    <div className={iconContainerStyle}>
                      <Icon
                        icon={sectionLink.logo}
                        style={18}
                        useDefaultFills={false}
                        className={iconStyle}
                      />
                      <span className={sectionItemTitleStyle}>
                        {sectionLink.title}
                      </span>
                    </div>
                    <div className={textContainerStyle}>
                      {sectionLink.subtitle && (
                        <p className={sectionItemSubtitle}>
                          {sectionLink.subtitle}
                        </p>
                      )}
                    </div>
                  </animated.a>
                )
              })}
            </div>
          )
        })}
      </div>
    </animated.div>
  )
}

export default DesktopNavigationSubSection
