/// <reference path="../../../../types/global.d.ts" />
import Analytics, { InvalidEvent } from './generated'

import { logWarning } from '../Util'

// This error handler fires only when Typewriter's runtimeValidation
// flag is enabled, which is only true in development.
const errorHandler = (error: InvalidEvent) => {
  const errorMessage = {
    type: 'Typewriter JSON Schema Validation Error',
    description:
      `You made an analytics call (${
        error.eventName
      }) using Typewriter that doesn't match the ` +
      'App Tracking Plan. This warning is only visible during development, and your ' +
      'analytics call will continue to fire in production.',
    error
  }
  logWarning(JSON.stringify(errorMessage, null, 2))
}

// Wrap analytics.js in order to lazy evaluate the value of `window.analytics`.
// We should explore a better method for this upstream in typewriter.
const wrappedAJS = {
  track: (event: string, props?: any, options?: any, callback?: any) =>
    window.analytics.track(event, props, options, callback)
}

export const trackingPlan = new Analytics(wrappedAJS, {
  onError: errorHandler
})
